<template>
  <div class="search">
    <el-card shadow="never">
      <el-container style="display: flex; align-items: center">
        <go-back style="margin-right: 25px" />

        <div style="width: 350px; margin-left: 10px">
          <el-input v-model="name" @input="checkName()" width="350" placeholder="Search Business"></el-input>
        </div>
      </el-container>
    </el-card>

    <div class="vendorsearchcontainer">
      <div
        v-if="this.unSearched === true || this.$store.state.searchedBusiness.length === 0"
        class="empty"
      >Search Business</div>
      <div v-if="this.empty=== true" class="empty">No Business matches this name</div>
      <div accordion v-for="business in searchedBusiness" :key="business.id">
        <div class="collapse">
          <el-button type="text" class="textbtn">{{ $format(business.name) }}</el-button>
          <div>
            <div>
                <div class="email">Email: {{$format(business.email) }}</div>
                <div class="email">Service Location: {{ $format(business.location ) }}</div>
              </div>
            <div class="modalbuttondiv">
              <el-button class="greenbtn addbtn" @click="apply(business.email)">Apply to Business</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { mapState } from "vuex";
export default {
  name: "search",
  data: () => {
    return {
      value: null,
      name: null,
      unSearched: true,
      empty: false,
      changeRoute: false
    };
  },
  computed: {
    ...mapState(["searchedBusiness"])
  },
  methods: {
    checkName() {
      this.loading = true;
      const headers = this.$store.state.jwt;
      const config = {
        headers
      };
      let api =
        this.$store.state.apiUrl +
        `/api/v1/accounts/business-search?q=${this.name}`;
      setTimeout(() => {
        axios
          .get(api, config)
          .then(result => {
              this.$store.dispatch("setSearchedBusiness", result.data.businesses);
            if (this.name.length !== 0) {
              if (result.data.products.length === 0) {
                this.empty = true;
              } else {
                this.empty = false;
              }
            }
          })
      }, 1000);
      if (this.name.length === 0) {
        this.$store.dispatch("setSearchedBusiness", []);
      }
    },
    apply(email) {
      const data = {
        email: email
      };
      const config = {
        headers: this.$store.state.jwt
      };
      const api =
        this.$store.state.apiUrl + "/api/v1/accounts/apply-to-business";
      axios
        .post(api, data, config)
        .then(response => {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: response.data.message
          });
          Toast.fire({
            type: "success",
            title: response.data.message
          });
        })
        .catch(err => {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "error"
          });
          Toast.fire({
            type: "error",
            title: err.response.data.message
          });
        });
    },
    search() {
      this.unSearched = false;
      const headers = this.$store.state.jwt;
      const config = { headers };

      setTimeout(() => {
        if (this.value === "vendor" || this.value === null) {
          const api =
            this.$store.state.apiUrl +
            `/api/v1/accounts/vendor-search?q=${this.name}&by=name`;
          axios
            .get(api, config)
            .then(result => {
              if (result.data.vendors.length === 0) {
                this.empty = true;
              } else {
                this.empty = false;
              }
              this.$store.dispatch("setSearchedBusiness", result.data.vendors);
            })
            .catch(() => {
              this.$store.dispatch("loader", false);
              this.loading = false;
            });
        } else if (this.value === "products") {
          axios
            .get(
              this.$store.state.apiUrl +
                `/api/v1/accounts/product-search?q=${this.name}`,
              config
            )
            .then(result => {
              if (result.data.products.length === 0) {
                this.empty = true;
              } else {
                this.empty = false;
              }
              this.$store.dispatch("setSearchedBusiness", result.data.products);
            });
        } else if (this.value === "category") {
          axios
            .get(
              this.$store.state.apiUrl +
                `/api/v1/accounts/vendor-search?q=${this.name}&by=service_category`,
              config
            )
            .then(result => {
              if (result.data.vendors.length === 0 && this.name !== null) {
                this.empty = true;
              } else {
                this.empty = false;
              }
              this.$store.dispatch("setSearchedBusiness", result.data.vendors);
            })
        } else if (this.value === "oem") {
          axios
            .get(
              this.$store.state.apiUrl +
                `/api/v1/accounts/product-search?q=${this.name}&by=oem`,
              config
            )
            .then(result => {
              if (result.data.products.length === 0) {
                this.empty = true;
              }
              this.$store.dispatch("setSearchedBusiness", result.data.products);
            });
        }
      }, 1500);

      if (this.name === null || this.name.length === 0) {
        this.$store.dispatch("setSearchedBusiness", []);
      }
    }
  }
};
</script>
<style scoped>
/* .search {
   display: flex; 
} */
.detailsnrate {
  display: flex;
  justify-content: space-between;
}
.empty {
  font-weight: bold !important;
  display: flex;
  justify-content: center;
  font-size: 30px !important;
  color: rgb(19, 19, 56);
}
.el-card {
  display: flex;
  flex-direction: column;
}
.collapse {
  font-size: 20px !important;
}
.title {
  font-size: 26px;
  font-weight: bold;
}
.searchItem {
  color: rgb(4, 97, 97);
}
.textbtn {
  font-size: 25px;
}
.searchitem {
  /* color: rgb(19, 19, 56); */
  font-size: 24px;
  font-weight: bold;
}
.vendorsearchcontainer {
  width: 900px;
  margin: auto;
  padding-top: 30px;
}
#el-collapse-head-2549 {
  font-size: 30px !important;
}
.el-collapse-item__header {
  font-size: 30px !important;
}
.addbtn {
  margin-top: 5px;
}
* {
  font-family: "Poppins", sans-serif !important;
}
.el-menu {
  width: 200px;
  padding-top: 30px;
  padding-bottom: 10px;
  margin-left: 40px;
  margin-top: 40px;
}
.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.namebtn {
  font-size: 22px;
}
