<template>
  <div class="body">
    <div id="modal" v-loading="loading" v-if="changeRoute === true"></div>
    <div class="admincontainer">
      <div class="admincontainerheader">
        <b>PENDING BUSSINESSES</b>
      </div>
      <div class="adminempty" v-if="this.$store.state.pendingBusinesses.length === 0 ">
        <div class="adminemptyheader">No pending business yet</div>
        <router-link to="/search-business">
          <el-button class="adminemptybutton emptybtn">Search to apply to business</el-button>
        </router-link>
      </div>
      <div class="admins" v-for="business in pendingBusinesses" :key="business.id">
        <div class="admin">
          <div class="creds">
            
            <el-avatar shape="square" class="avatar" :src="business.avatar" ></el-avatar>
            <h3 class="adminname">{{ $format(business.name )  }}</h3>
          </div>
          <!--  -->
          <div class="tags">
            <div class="redtext">Pending</div>
            <el-button class="greenbtn" @click="fulfillreq(business)">Review Application</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
.body {
  width: 100%;
  padding-bottom: 5%;
}
.head {
  display: flex;
  justify-content: space-between;
  width: 800px;
  margin-left: 14%;
  padding-top: 15px;
}
.redtext {
  color: red;
  margin-right: 15px;
}
.tags {
  display: flex;
  align-items: center;
}
</style>
<script>
import axios from "axios";
import { mapState } from "vuex";
export default {
  data: () => {
    return {
      card: true,
      changeRoute: false,
      loading: true
    };
  },
  beforeCreate() {
    this.$store.dispatch("getPendingBusinessList");
  },
  mounted() {
  },
  computed: {
    ...mapState(["pendingBusinesses"])
  },
  methods: {
    async fulfillreq(business) {
      this.changeRoute = true;
      const jwt = this.$store.state.jwt;
      const config = {
        headers: jwt
      };
      await axios
        .get(
          this.$store.state.apiUrl +
            "/api/v1/details/business/" +
            business.businessId,
          config
        )
        .then(response => {
          this.$store.state.pendingBusinessDetailsAvatar = business.avatar;
          this.$store.state.pendingBusinessDetails = response.data.business;
        });
      await axios
        .get(
          this.$store.state.apiUrl +
            `/api/v1/see-fulfillments?businessId=${business.businessId}&from=vendor`,
          config
        )
        .then(response => {
          this.$store.state.existingFulfillments =
            response.data.data.requirements;
            this.$store.state.fulfillmentsresponse = response.data.data;
        })
        .catch(error => {
          throw error;
        });
      this.$router.push("/review-application");
    }
  }
};
</script>
