<template>
  <div class="main">
    <el-card auth-card>
      <div>
        <div class="header">Sign In</div>
        <div class="writeup">
          Not a member?
          <router-link to="/register">Register</router-link>
        </div>
      </div>
      <el-form ref="form" form>
        <el-form-item class="item">
          <el-input v-model="data.email" placeholder="Email"></el-input>
        </el-form-item>
        <el-form-item class="item">
          <el-input
            v-model="data.password"
            show-password
            placeholder="Password"
          ></el-input>
        </el-form-item>

        <el-button class="btn" @click="submitForm()" v-if="loading === false"
          >Login</el-button
        >
        <el-form-item v-loading="loading" class="buttonpadding"></el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import auth from "../../auth/index.js";
export default {
  data: () => {
    return {
      loading: false,
      data: {
        email: null,
        password: null
      },
      fullscreenLoading: false
    };
  },
  components: {},
  mounted() {
  },
  methods: {
    async submitForm() {
      const vueApp = this;
      this.loading = true;
      auth.validEmail(this.data.email);
      if (auth.validEmail(this.data.email)) {
        try {
          await auth.login(this.data, vueApp);
        } catch (_) {
          this.loading = false;
        }
      } else {
        alert("Incorrect Email");
        this.loading = false;
      }
    }
  }
};
</script>
<style scoped>
