import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "vue-material-design-icons/styles.css";
import VueSweetalert2 from "vue-sweetalert2";
import "sweetalert2/dist/sweetalert2.min.css";
import VueTelInput from "vue-tel-input";
import Breadcrumb from "./components/breadcrumb.vue";
import "./plugins/element.js";
import ElementUI from "element-ui";
import locale from "element-ui/lib/locale/lang/en";
import VueI18n from "vue-i18n";
import "mdi-vue/CommentAlert.vue";
import goback from "./components/goback.vue";
import historygoback from "./components/historygoback.vue";
import Cloudinary from "cloudinary-vue";
import {format, formatText} from '@/helpers/index.js'

Vue.use(VueTelInput);
Vue.use(VueSweetalert2);
Vue.use(VueI18n);
Vue.use(Cloudinary, {
  configuration: {
    cloudName: "demo"
  }
});
Vue.component("go-back", goback);
Vue.component("bread-crumb", Breadcrumb);
Vue.component("history-go-back", historygoback)
Vue.use(ElementUI, { locale });
Vue.config.productionTip = false;
Vue.config.locale = "en";
Vue.prototype.$format = format
Vue.prototype.$formatText = formatText

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
