<template>
  <div class="body">
    <!-- <div id="modal" v-if="pageLoading === true"></div> -->
    <div>
      <div id="modal" v-if="modalShow === true" style="padding-bottom: 400px !important;">
        <div v-if="
             viewReq === true
          ">
          <div class="admincontainer" v-loading="pageLoading">
            <div class="admincontainerheader">
              <b>STATUTORY REQUIREMENTS</b>
              <div class="modalbuttondiv">
                <el-button class="modalclosebtn" @click="modalHide()">
                  <i class="el-icon-close"></i>
                </el-button>
              </div>
            </div>
            <div
              class="admins"
              v-for="fulfillment in existingFulfillments.statutory"
              :key="fulfillment.id"
            >
              <div class="admin" style="background-color: white; padding: 10px; height: 100px;">
                <div
                  style="align-self: flex-start; height: 100%; display: flex; flex-direction: column; justify-content: space-around"
                >
                  <div style="font-weight: bold; font-size: 20px">
                    {{
                    $format(pendingBusinessDetails.requirements.statutory[
                    fulfillment.id
                    ].name)
                    }}
                  </div>
                  <div v-if="fulfillment.value === true" style="color: grey; font-size: 15px">Yes</div>
                  <div v-if="fulfillment.value === false">No</div>
                </div>
              </div>
            </div>
          </div>
          <div class="admincontainer" style="margin-bottom: 150px " v-loading="pageLoading">
            <div class="admincontainerheader">
              <b>DOCUMENTS REQUIREMENTS</b>
            </div>
            <div
              class="admins"
              v-for="fulfillment in existingFulfillments.document"
              :key="fulfillment.id"
            >
              <div class="admin" style="background-color: white; padding: 10px; height: 100px;">
                <div
                  style="align-self: flex-start; height: 100%; display: flex; flex-direction: column; justify-content: space-around"
                >
                  <div>
                    {{ $format(
                    pendingBusinessDetails.requirements.document[
                    fulfillment.id
                    ].name)
                    }}
                  </div>
                  <a :href="fulfillment.value">Document</a>
                </div>
                <!-- <div style="align-self: flex-end">
                  <el-button
                    class="greenbtn"
                    @click="action(fulfillment.id, 'document', 'approve')"
                  >Approve</el-button>
                  <el-button @click="action(fulfillment.id, 'document', 'reject')">Reject</el-button>
                </div> -->
              </div>
            </div>
            <div style="display: flex; background-color: #ffffff; padding: 5px;">
              <el-button class="greenbtn" @click="makeVendor()" style="margin-left: 5px">Add Vendor</el-button>
              <el-button @click="viewReq = false; rejectComment = true" style="margin-left: 5px">Send back for review</el-button>
              <el-button
                @click="viewReq = false; rejectNoComent = true"
                style="margin-left: 5px; color: #ffffff; background-color: red;"
              >Reject and Close</el-button>
            </div>
          </div>
        </div>
        <el-card class="modalcard" v-if="rejectComment === true">
          <h3>Reason for rejecting</h3>
          <textarea
          v-model="comment"
            name
            id
            cols="48"
            rows="10"
            style="background-color: #eee; border: transparent; outline: none;"
          ></textarea>
          <div style=" margin-top: 6px">
            <el-button class="del-action-pop-up-btn" @click="rejectVendor();rejectComment = true">Reject</el-button>
            <el-button @click="viewReq = true;rejectComment = false">Cancel</el-button>
          </div>
        </el-card>
        <el-card class="modalcard" v-if=" rejectNoComent === true">
          <div class="del-pop-up-btn">
            <i class="el-icon-delete"></i>
          </div>
          <div class="del-pop-up-header">Reject vendor</div>
          <div class="del-pop-up-text">Are you sure you want to Reject this vendor</div>
          <div style="display: flex; justify-content: center; margin-top: 4px">
            <el-button class="del-action-pop-up-btn" @click="rejectVendor()">Reject</el-button>
            <el-button @click=" rejectNoComent= false; viewReq = true">Cancel</el-button>
          </div>
        </el-card>
        <el-card class="modalcard" v-if="del === true">
          <div class="del-pop-up-btn">
            <i class="el-icon-delete"></i>
          </div>
          <div class="del-pop-up-header">delete vendor</div>
          <div class="del-pop-up-text">Are you sure you want to delete this vendor</div>
          <div style="display: flex; justify-content: center; margin-top: 4px">
            <el-button class="del-action-pop-up-btn" @click="delist()">Delete</el-button>
            <el-button @click="closeDelPopUp()">Dont Delete</el-button>
          </div>
        </el-card>
        <el-card class="modalcard" v-if="cardshow === true">
          <div class="modalbuttondiv">
            <el-button circle class="modalclosebtn" @click="closeModal()">
              <i class="el-icon-close"></i>
            </el-button>
          </div>
          <el-avatar
            class="modalavatar"
            src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
          ></el-avatar>
          <el-form class="form" ref="form" id="form">
            <el-form-item label="Vendor's Email" label-position="top">
              <el-input v-model="userData.email" required></el-input>
            </el-form-item>
            <el-form-item v-loading="loading">
              <el-button class="modalbtn" @click="inviteVendor()" v-if="loading === false">
                <span id="text">Invite Vendor</span>
              </el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </div>
    </div>
    <div class="adminheader">
      <el-autocomplete v-model="name" placeholder="Please input" class="admininput">
        <i class="el-icon-search el-input__icon" slot="suffix"></i>
      </el-autocomplete>
      <el-button class="greenbtn" @click="card()">Invite Vendor</el-button>
    </div>
    <div class="admincontainer">
      <div class="admincontainerheader">
        <b>Present Vendors</b>
      </div>
      <div class="adminempty" v-if="this.$store.state.vendors.length === 0">
        <div class="adminemptyheader">No Vendors yet</div>
        <el-button class="adminemptybutton emptybtn" @click="card()">Add Vendor by email</el-button>
      </div>
      <div class="admins" v-for="vendor in vendors" :key="vendor.vendorId">
        <div class="admin">
          <div class="admincreds">
            <el-avatar shape="square" :src="vendor.avatar"></el-avatar>
            <el-button class="adminname namebtn" type="text">{{ $format(vendor.name )}}</el-button>
          </div>
          <span class="pending" v-if="vendor.accepted === false">Pending</span>
          <div id="modal" v-if="rateModal === true">
            <el-card style="width: 350px;margin: auto;  height: 220px padding: 0px">
              <div class="modalbuttondiv" style="margin: 0px">
                <el-button class="modalclosebtn" @click="rateClose()">
                  <i class="el-icon-close"></i>
                </el-button>
              </div>
              <div>
                <div style="display: flex; justify-content: center; flex-direction: column">
                  <div class="ratetext" style="margin:auto">Rate {{$format(vendor.name)}}</div>
                  <el-rate v-model="rating" style="margin: auto; margin-top: 0px"></el-rate>
                  <el-button @click="rateVendor(vendor.vendorId)" class="ratebtn">Rate</el-button>
                </div>
              </div>
            </el-card>
          </div>
          <el-tooltip
            class="item"
            effect="dark"
            content="Click to rate this Vendor"
            placement="top-start"
          >
            <el-button
              id="ratebutton"
              v-if="vendor.accepted === true"
              @click="rateOpen()"
              style="background-color: transparent;border: transparent;color: gold; font-size: 18px"
            >
              <i class="el-icon-star-off"></i>
            </el-button>
          </el-tooltip>
          <el-button
            id="ratebutton"
            v-if="vendor.accepted === true"
            @click="initiateChat(vendor.vendorId)"
            style="background-color: transparent;border: transparent;color: blue; font-size: 24px"
          >
            <i class="el-icon-chat-dot-square"></i>
          </el-button>
          <el-button
            v-if="vendor.accepted === false"
            class="greenbtn"
            @click="review(vendor.vendorId)"
            style="margin-left: 5px"
          >Review Application</el-button>
          <el-button class="delbtn" @click="delPopUp(vendor)">
            <i class="el-icon-loading" v-if="loading === true"></i>
            <span v-if="loading === false">Delete</span>
          </el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { post } from "../../helpers/index.js";
// import auth from '../../auth/index.js'
import { mapState } from "vuex";
export default {
  data() {
    return {
       rejectNoComent: false,
       rejectComment: false,
      rating: null,
      centerDialogVisible: false,
      apiUrl: this.$store.state.apiUrl + "/api/v1/accounts/invite-vendor",
      userData: {
        email: null
      },
      users: [],
      name: null,
      timeout: null,
      loading: false,
      modalShow: false,
      cardshow: false,
      vendorId: null,
      rateModal: false,
      del: false,
      delVendor: null,
      viewReq: false,
      pageLoading: false,
      comment: null
    };
  },
  computed: {
    ...mapState(["vendors", "existingFulfillments", "pendingBusinessDetails"])
  },
  beforeCreate() {
    this.$store.dispatch("getVendorsCategory");
    this.$store.dispatch("getVendorsList");
  },
  methods: {
    rateOpen() {
      this.rateModal = true;
    },
    rateClose() {
      this.rateModal = false;
    },
    rateVendor(id) {
      const config = {
        headers: this.$store.state.jwt
      };
      let data = {};

      axios
        .post(
          this.$store.state.apiUrl +
            `/api/v1/accounts/business/rate?vendorId=${id}&rating=${this.$rating}`,
          data,
          config
        )
        .then(() => {
          const button = document.querySelector("#button");
          button.disabled = true;
        });
      this.rating = null;
      this.rateModal = false;
    },
    card() {
      this.cardshow = true;
      this.modalShow = true;
    },
    modal() {
      this.$store.state.card = false;
    },
    modalHide() {
      this.modalShow = false;
      this.viewReq = false;
    },
    initiateChat(vendorId) {
      const vueApp = this;
      this.$store.dispatch("initiateChat", { vendorId, vueApp });
    },
    closeModal() {
      this.cardshow = false;
      this.modalShow = false;
    },
    delPopUp(vendor) {
      this.del = true;
      this.modalShow = true;
      this.delVendor = vendor;
    },
    closeDelPopUp() {
      this.del = false;
      this.modalShow = false;
      this.delVendor = null;
    },
    delist() {
      this.loading = true;
      const headers = this.$store.state.jwt;
      const data = {
        email: this.delVendor.email
      };
      axios
        .delete(this.$store.state.apiUrl + "/api/v1/accounts/vendors", {
          data,
          headers
        })
        .then(result => {
          this.$store.state.vendors.splice(
            this.$store.state.vendors.indexOf(this.delVendor),
            1
          );
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: result.data.message
          });
          Toast.fire({
            type: "success",
            title: result.data.message
          });
          this.loading = false;
        })
        .catch(err => {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: err.response.data.message
          });
          Toast.fire({
            type: "success",
            title: err.response
          });
          this.loading = false;
        });
      this.del = false;
      this.modalShow = false;
      this.delVendor = null;
    },
    rejectVendor() {
      this.loading = true;
      const userData = {
        vendorId: this.vendorId,
        review: this.rejectComment,
      };
      if(this.rejectComment === true) {
      userData.comment = this.comment
      }
      post(
        this.$store.state.apiUrl + "/api/v1/accounts/business/reject",
        userData,
        this,
        () => {
          this.rejectNoComent = false
          this.rejectComment = false
          this.cardshow = false;
          this.modalShow = false;
          this.loading = false;
        }
      );
    },
    makeVendor() {
      this.loading = true;
      const userData = {
        vendorId: this.vendorId
      };
      post(
        this.$store.state.apiUrl + "/api/v1/accounts/business/approve",
        userData,
        this,
        () => {
          this.$store.dispatch("getVendorsList");
          this.cardshow = false;
          this.modalShow = false;
          this.loading = false;
        }
      );
    },
    inviteVendor() {
      this.loading = true;
      let email = this.userData.email;

      const userData = { email };
      if (email !== null) {
        post(
          this.$store.state.apiUrl + "/api/v1/accounts/invite-vendor",
          userData,
          this,
          () => {
            this.$store.dispatch("getVendorsList");
            this.userData.email = null;
            this.loading = false;
            this.modalShow = false;
            this.cardshow = false;
          }
        );
      } else {
        this.loading = false;
        this.modalShow = false;
        this.cardshow = false;
        alert("Provide vendor email");
      }
    },
    async review(id) {
      this.pageLoading = true;
      this.vendorId = id;
      this.modalShow = true;
      this.viewReq = true;
      const config = {
        headers: this.$store.state.jwt
      };
      await axios
        .get(
          this.$store.state.apiUrl +
            `/api/v1/see-fulfillments?vendorId=${id}&from=business`,
          config
        )
        .then(response => {
          this.$store.state.existingFulfillments =
            response.data.data.requirements;
        });
      await axios
        .get(
          this.$store.state.apiUrl +
            "/api/v1/details/business/" +
            this.$store.state.userInfo.id,
          config
        )
        .then(response => {
          this.$store.state.pendingBusinessDetails = response.data.business;
          this.pageLoading = false;
        });
    },
    async action(id, type, actionName) {
      await axios
        .post(
          `${this.$store.state.apiUrl}/api/v1/fulfillments/${actionName}`,
          { id, type },
          { headers: this.$store.state.jwt }
        )
        .then(response => {
          this.$store.dispatch("getVendorsList");
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: response.data.message
          });
          Toast.fire({
            type: "success",
            title: response.data.message
          });
        });
    }
  }
};
</script>
<style scoped>
.body {
  width: 100%;
  padding-bottom: 5%;
}
.ratebtn {
  border: 1px solid rgb(69, 218, 69);
  color: rgb(69, 218, 69);
  margin: auto;
  width: fit-content;
  margin-top: 10px;
}
.modal {
  overflow: auto !important;
  max-height: 80vh;
}
.ratetext {
  font-size: 18px;
  margin-bottom: 20px !important;
}
body {
  overflow: auto;
}
.card {
  width: 600px;
}
.id {
  margin-top: 1%;
  margin-left: 4%;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.modalbtn {
  width: 100%;
  background-color: #0066f5;
  color: #ffffff;
}
.modalavatar {
  margin: auto;
  display: flex;
  justify-self: center;
}
.adminavatar {
  width: 100px;
  height: 100px;
  margin: auto;
}
.dropdownmenu {
  padding: 15px;
  border-radius: 15px;
}
.field {
  margin-bottom: 0px;
}
.dropdown {
  width: 100%;
}
.namebtn {
  margin-left: 9px;
}
.dropdownheader {
  font-weight: bold;
  margin-right: 5px;
  font-size: 13px;
}
.dropdownitem {
  font-size: 13px;
}
.pending {
  color: red;
}
@media screen and (max-width: 600px) {
  .pending {
    display: none;
  }
}
</style>
