<template>
  <div class="body">
    <div class="d-flex justify">
      <history-go-back class="go-back" />
      <div></div>
    </div>
    <div class="background">
      <div class="formcont">
        <div class="breadcrumb">
          <div class="txt">Send Quote for {{ $format(this.$store.state.rfpId.title) }}</div>
        </div>
        <el-form class="form" id="myForm">
          <el-form-item label="Description Of Item" label-position="top">
            <el-input
              type="textarea"
              autosize
              placeholder="Please input"
              v-model="data.description"
            ></el-input>
          </el-form-item>
          <el-form-item label="Original Equipment Manufacturer" label-position="top">
            <el-input v-model="data.oem"></el-input>
          </el-form-item>
          <el-form-item label="Quantity" label-position="top">
            <el-input v-model="data.quantity" type="number"></el-input>
          </el-form-item>
          <el-form-item label="Delivery Date" label-position="top">
            <el-input v-model="data.delivery_date" type="date"></el-input>
          </el-form-item>
          <el-form-item label="Currency">
            <select v-model="data.currency" placeholder="Pick Currency" id="select">
              <option
                v-for="option in currencies"
                :key="option.name"
                :label="option.name"
                :value="option.name"
                id="option"
              ></option>
            </select>
          </el-form-item>
          <el-form-item label="Price of Item" label-position="top">
            <el-input type="Number" v-model="data.price"></el-input>
          </el-form-item>
          <span>
            <el-button class="pinkbtn" @click="sendRfq()">
              <span id="text">Add Product</span>
              <i class="el-icon-loading" id="load"></i>
            </el-button>
          </span>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "quotation",
  data() {
    return {
      data: {
        description: null,
        rfq_id: this.$store.state.rfpId.id,
        price: null,
        oem: null,
        quantity: null,
        delivery_date: null,
        currency: null
      },
      currencies: [
        {  name: "US Dollar" },
        {  name: "Euro" },
        { name: "Japanese Yen" },
        {  name: "Australian Dollar" },
        {  name: "Canadian Dollar" },
        {  name: "Swiss Franc" },
        {  name: "Chinese renminbi" },
        {  name: "Swedish krona" },
        {  name: "New Zealand dollar " },
        {  name: "Nigerian Naira" },
      ]
    };
  },
  methods: {
    goback() {},
    sendRfq() {
     let presentDate = new Date(), givenDate = new Date(this.data.delivery_date)
     if(presentDate <= givenDate) {
      var load = document.getElementById("load");
      var text = document.getElementById("text");
      load.style.display = "block";
      text.style.display = "none";
      var config = {
        headers: this.$store.state.jwt
      };
      var api = this.$store.state.apiUrl + "/api/v1/quotes";
      axios
        .post(api, this.data, config)
        .then(response => {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success"
          });
          Toast.fire({
            type: "success",
            title: response.data.message
          });
          load.style.display = "none";
          text.style.display = "block";
          this.$router.push("/explore");
        })
        .catch(err => {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "error"
          });
          Toast.fire({
            type: "error",
            title: err.response.data.message
          });
          load.style.display = "none";
          text.style.display = "block";
        });
      document.getElementById("myForm").reset();
     } else {  
       const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "Error"
          });
          Toast.fire({
            type: "error",
            title: 'Invalid Date'
          });
     }
    }
  }
};
</script>
<style scoped>
.body {
  padding-top: 15px;
}
.go-back {
  margin-left: 15px;
}
/* .background {
} */
.justify {
  justify-content: space-between;
  padding: 5px;
}
.txt {
  font-size: 25px;
  font-family: "Poppins", sans-serif;
}
.el-select {
  width: 100%;
}
.formcont {
  width: 500px;
  display: flex;
  flex-direction: column;
  margin: auto;
}
.breadcrumb {
  margin: auto;
  text-align: center;
}
.form {
  width: 100%;
}
.heading {
  width: inherit;
  margin-top: -3%;
  margin-right: 3%;
  margin-bottom: 30px;
  border-bottom: 1px dashed rgb(235, 224, 224);
}
.uploadbutton {
  background-color: #0066f5;
  width: 100%;
  color: #ffffff;
}
.bluebtn {
  background-color: #0066f5;
  width: 30%;
  color: #ffffff;
}
.pinkbtn {
  color: #fff;
  background-color: #28a745;
  background-image: linear-gradient(-180deg, #34d058, #28a745 90%);
}
.btn {
  width: 30%;
  color: #0066f5;
  border: 1px solid #0066f5;
}
#load {
  display: none;
  font-size: 20px;
  color: #ffffff;
}
#select {
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: 0;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}
</style>
