<template>
  <div>
    <div id="modal" v-if="reject === true">
      <el-card class="modalcard">
        <h3>Reason for reject</h3>
        <textarea name id cols="48" rows="10" style="background-color: #eee; border: transparent;"></textarea>
        <div style=" margin-top: 6px">
          <el-button class="del-action-pop-up-btn" @click="deleteInventory() ">Delete</el-button>
          <el-button @click="this.reject = false">Cancel</el-button>
        </div>
      </el-card>
    </div>
    <div class="title">VIEW QUOTES SENT BY VENDORS</div>
    <div v-if="quotes.length === 0" style="text-align: center; line-height: 30px;padding: 40px;">
      <div style="font-size: 18px;font-weight: bold;">No quotes been sent yet.</div>
      <div>
        Create
        <router-link to="/rfq">RFQ's</router-link>to recieve quotes
      </div>
    </div>
    <div class="quotes" v-if="quotes.length !== 0">
      <el-table style="width: 100%" :data="quotes" class="quotes">
        <el-table-column type="expand">
          <template slot-scope="props">
            <p>Name of Vendor: {{ $format(props.row.vendor.name) }}</p>
            <p>Title of RFP: {{ props.row.rfq.title }}</p>
            <p>Description of Quote: {{ props.row.description }}</p>
            <p>Price of Item: {{ props.row.price }}</p>
            <p>Quantity of Quote items: {{ props.row.quantity}}</p>
            <div class="buttons">
              <el-button @click="acceptQuote(props.row.id)" class="greenbtn">
                <span v-if="loading == false">Accept Quote</span>
                <i class="el-icon-loading" v-if="loading == true"></i>
              </el-button>
              <el-button @click="rejectQuote(props.row.id)" class="del-action-pop-up-btn">
                <span v-if="loading == false">Reject Quote</span>
                <i class="el-icon-loading" v-if="loading == true"></i>
              </el-button>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="Title Of RFP" prop="rfq.title"></el-table-column>
        <el-table-column label="Price" prop="price"></el-table-column>
        <el-table-column label="Vendor" prop="vendor.name"></el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
import { post } from "../../helpers/index.js";
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(["quotes"])
  },
  data() {
    return {
      loading: false,
      reject: false,
      rejectId: null
    };
  },
  beforeCreate() {
    this.$store.dispatch("getQuotes");
  },
  methods: {
    async acceptQuote(id) {
      let vueApp = this;
      try {
        await post(
          this.$store.state.apiUrl + "/api/v1/business/quotes",
          { id: id },
          vueApp
        );
        this.$store.dispatch("getQuotes");
      } catch (_) {
        this.loading = true;
      }
    },
    rejectQuote(id) {
      this.reject = true;
      this.rejectId = id;
    }
  }
};
</script>
<style scoped>
.quotes {
  width: 800px;
  margin: auto;
}
.icon {
  font-size: 25px;
}
.title {
  text-align: center;
  padding: 20px;
}
</style>
