<template>
  <div class="requirements">
    <div id="modal" v-if="del === true">
      <el-card class="modalcard">
        <div class="del-pop-up-btn">
          <i class="el-icon-delete"></i>
        </div>
        <div class="del-pop-up-header">Delete Requirement</div>
        <div class="del-pop-up-text">Are you sure you want to delete this requirement</div>
        <div style="display: flex; justify-content: center; margin-top: 4px">
          <el-button class="del-action-pop-up-btn" @click="delet()">Delete</el-button>
          <el-button @click="closeDelPopUp()">Dont Delete</el-button>
        </div>
      </el-card>
    </div>
    <h2 style="margin-left: 140px; margin-bottom: 0px">Requirements</h2>
    <div class="d-flex space-between width">
      <el-button type="text" @click="goBack()" class="btn">
        <i class="el-icon-back"></i>
      </el-button>
    </div>
    <div class="admincontainer">
      <div class="admincontainerheader">STATUTORY REQUIREMENTS</div>
      <div>
        <div
          class="admin"
          v-for="requirement in statutoryRequirements"
          :key="requirement.id"
          style="padding:10px"
        >
          <div>{{ requirement.name.name }}</div>
          <div class="reqbtns">
            <!-- <el-button @click="fill(requirement, 'statutory')">
              <i class="el-icon-edit"></i>
            </el-button>-->
            <el-button class="delbtn" @click="dele(requirement, 'statutory')">
              <i class="el-icon-delete"></i>
            </el-button>
          </div>
        </div>
        <div
          class="admin"
          style="padding:10px; justify-content: space-between; display: flex"
          v-if="edits !== null"
        >
          <div style="flex: 1; margin-right: 10px">
            <el-form id="form">
              <el-input
                placeholder="Update requirement"
                v-model="data.statutoryedit"
                required
                maxlength="20"
              ></el-input>
            </el-form>
          </div>
          <div class="reqbtns">
            <el-button class="greenbtn" @click="update()">
              <i class="el-icon-edit"></i>
            </el-button>
          </div>
        </div>
        <div class="admin" style="padding:10px; justify-content: space-between; display: flex">
          <div style="flex: 1; margin-right: 10px" v-if="statutoryRequirements.length < 10">
            <el-form id="form">
              <el-input placeholder="requirement" v-model="data.statutory" maxlength="30"></el-input>
            </el-form>
          </div>
          <div class="reqbtns" v-if="statutoryRequirements.length < 10">
            <el-button class="greenbtn" @click="upload('statutory')"  :disabled="disable">
              <i class="el-icon-plus"></i>
            </el-button>
          </div>
          <div
            style="padding: 10px;"
            v-else
          >You have exceeded the maximun of 10 document Requirements</div>
        </div>
      </div>
    </div>
    <div class="admincontainer" style="margin-bottom: 40px;">
      <div class="admincontainerheader">DOCUMENT REQUIREMENTS</div>
      <div>
        <div
          class="admin"
          v-for="requirement in documentRequirements"
          :key="requirement.id"
          style="padding:10px"
        >
          <div>{{ requirement.name.name }}</div>
          <div class="reqbtns">
            <el-button class="delbtn" @click="dele(requirement, 'document')">
              <i class="el-icon-delete"></i>
            </el-button>
          </div>
        </div>
        <div
          class="admin"
          style="padding:10px; justify-content: space-between; display: flex"
          v-if="edit !== null"
        >
          <div style="flex: 1; margin-right: 10px">
            <el-form id="form">
              <el-input placeholder="Update requirement" v-model="data.documentreqedit" required></el-input>
            </el-form>
          </div>
          <div class="reqbtns">
            <el-button class="greenbtn" @click="disable = true; update()">
              <i class="el-icon-edit"></i>
            </el-button>
          </div>
        </div>
        <div class="admin" style="padding:10px; justify-content: space-between; display: flex">
          <div style="flex: 1; margin-right: 10px" v-if="documentRequirements.length < 10">
            <el-form id="form">
              <el-input
                placeholder="requirement"
                v-model="data.documentreq"
                required
                maxlength="30"
              ></el-input>
            </el-form> 
          </div>
          <div class="reqbtns" v-if="documentRequirements.length < 10">
            <el-button class="greenbtn" @click="disable = true; upload('document')" :disabled="disable">
              <i class="el-icon-plus"></i>
            </el-button>
          </div>
          <div
            style="padding: 10px;"
            v-else
          >You have exceeded the maximun of 10 document Requirements</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { mapState } from "vuex";
import { post } from "../helpers/index.js";
export default {
  data() {
    return {
      loading: false,
      data: {
        statutory: "",
        documentreq: "",
        statutoryedit: "",
        documentreqedit: ""
      },
      edit: null,
      edits: null,
      del: false,
      req: null,
      type: null,
      disable: false
    };
  },
  beforeCreate() {
    this.$store.dispatch("getRequirements");
  },
  computed: {
    ...mapState(["documentRequirements", "statutoryRequirements"])
  },
  methods: {
    closeDelPopUp() {
      this.del = false;
    },
    dele(requirement, type) {
      this.del = true;
      this.req = requirement;
      this.type = type;
    },
    async upload(type) {
      let data;
      let url = this.$store.state.apiUrl + "/api/v1/accounts/requirements";
      if (type === "statutory") {
        if (this.$store.state.statutoryRequirements.length < 10) {
          data = {
            requirement: this.data.statutory,
            type: type
          };
          try {
            await post(url, data, this, () => {
              this.$store.dispatch("getRequirements");
            });
          } catch (_) {
            this;
          }
        }
      } else if (type === "document") {
        data = {
          requirement: this.data.documentreq,
          type: type
        };
        if (this.$store.state.statutoryRequirements.length < 10) {
          try {
            await post(url, data, this, () => {
              this.$store.dispatch("getRequirements");
            });
          } catch (_) {
            this;
          }
        }
      
      this.disable = false
      }
      this.loading = true;

      this.data.documentreq = "";
      document.getElementById("form").reset();
    },
    delet() {
      const data = {
        requirement_id: this.req.id,
        type: this.type
      };
      const headers = this.$store.state.jwt;
      axios
        .delete(this.$store.state.apiUrl + "/api/v1/accounts/requirements", {
          data,
          headers
        })
        .then(response => {
          this.$store.dispatch("getRequirements");
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: response.data.message
          });
          Toast.fire({
            type: "success",
            title: response.data.message
          });
        });
      this.del = false;
    },
    goBack() {
      window.history.back();
    },
    fill(requirement, type) {
      if (type === "document") {
        this.edit = {
          requirement: requirement,
          type: type
        };
      } else if (type === "statutory") {
        this.edits = {
          requirement: requirement,
          type: type
        };
      }
    },
    update() {
      let data;
      const headers = this.$store.state.jwt;
      const config = { headers };
      if (this.edits.type !== null) {
        data = {
          new_requirement: this.data.statutoryedit,
          requirement_id: this.edits.requirement.id,
          type: this.edits.type
        };
      }
      if (this.edit.type !== null) {
        data = {
          new_requirement: this.data.documentreqedit,
          requirement_id: this.edits.requirement.id,
          type: this.edit.type
        };
      }
      axios
        .put(
          this.$store.state.apiUrl + "/api/v1/accounts/requirements",
          data,
          config
        )
        .then(result => {
          this.$store.dispatch("getRequirements");
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: result.data.message
          });
          Toast.fire({
            type: "success",
            title: result.data.message
          });
        });
      this.edit = null;
      this.edits = null;
    }
  }
};
</script>
<style scoped>
.requirements {
  overflow-x: hidden;
}
.btn {
  color: rgb(19, 19, 56);
  font-size: 30px;
}
.width {
  width: 60%;
  margin-left: 14%;
}
@media screen and (min-width: 601px) {
  .btn {
    display: none;
  }
  .width {
    justify-content: flex-end;
  }
}
@media screen and (max-width: 1055px) and (min-width: 601px) {
  .width {
    width: 95%;
    margin-left: 0%;
  }
}
@media screen and (max-width: 600px) {
  .btn {
    display: block;
  }
}
</style>
