<template>
  <div class="inventory">
    <div id="modal" v-if="loading === true" v-loading="loading"></div>
    <div class="tablediv">
      <div class="justify">
        <div style="display: flex; ">
          <history-go-back />
        </div>
        <div class="explore">
          <b>EXPLORE BUSINESS RFP's</b>
        </div>
      </div>
      <div class="emptycontainer" v-if="this.$store.state.exploreBusiness.length === 0">
        <div class="emptytitle">No RFP's Yet </div>
        <div class="emptytext">Try applying to businesses to stand a better chance </div>
      </div>
      <div class="cards" v-if="this.$store.state.exploreBusiness.length !== 0">
        <div v-for="rfp in exploreBusiness" :key="rfp.id">
          <el-card class="card" :body-style="{ padding: '0px' }">
            <div slot="header" class="clearfix">
              <span>
                <b>{{$format(rfp.title)}}</b>
              </span>
            </div>
            <div detaildiv>
              <div class="product">
                from:
                <b>{{$format(rfp.business.name) }}</b>
              </div>
              <div class="details">{{$formatText(rfp.description) }}</div>
            </div>
            <div class="actions">
              <div class="btnpromotion actionbtn" @click="sendQuote(rfp)">
                <el-button type="text" class="actionbutton">
                  Respond to RFP
                </el-button>
              </div>
              <div class="btnviewmore actionbtn" @click="view(rfp)">
                <el-button type="text" class="actionbutton">View More</el-button>
              </div>
            </div>
          </el-card>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import axios from 'axios'
export default {
  computed: {
    ...mapState(["exploreBusiness"])
  },
  mounted() {},
  beforeCreate() {
    this.$store.dispatch('explore')
  },
  methods: {
    sendQuote(id) {
      this.$store.state.rfpId = id;
      this.$router.push("/quoatation");
    },
    view(rfp) {
      this.loading = true;
      axios
        .get(this.$store.state.apiUrl + `/api/v1/rfqs/${rfp.id}`, {
          headers: this.$store.state.jwt
        })
        .then(result => {
          this.$store.state.viewRfpSender = rfp;
          this.$store.state.viewRfp = result.data.data;
          this.$router.push("/rfp-details");
          this.loading = false;
        });
    }
  }
};
</script>
<style scoped>
.emptycontainer{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 20px;
}
.emptytitle{
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
}
[placeholder] {
  object-fit: cover;
  object-position: 50%;
  height: 250px;
  width: 100%;
}
.tablediv {
  width: 100%;
}
.justify {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
[detaildiv] {
  font-size: 15px;
  padding: 0 10px 10px;
}
.product {
  padding: 10px;
  font-size: 16px;
  color: rgb(19, 19, 56);
}
.cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr ;
  grid-gap: 20px;
}
.explore {
  margin: auto;
  text-align: center !important;
  display: flex;
  justify-content: center;
  color: rgb(19, 19, 56);
}
.inventory {
  margin: auto;
  background-color: #ffffff;
  width: 90%;
  padding-top: 25px;
  display: flex;
}
.actionbutton {
  color: #409eff !important;
}
.actionbtn:hover {
  border: 1px solid #409eff;
}
.btnpromotion {
  width: 50%;
  border-right: 1px solid #eee;
  text-align: center;
}
.el-card {
  height: fit-content;
  margin: 15px;
  border-radius: 5px;
  padding-bottom: 0px;
  width: 350px;
  line-height: 25px;
  max-height: 400px;
}
.actions {
  display: flex;
  border-top: 1px solid #eee;
}
.details {
  word-wrap: break-word;
  padding: 3px;
  font-size: 16px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  max-height: 300px !important;
  padding-left: 10px;
  color: rgb(19, 19, 56);
}
.btnviewmore {
  width: 50%;
  text-align: center;
}
.btn {
  color: rgb(19, 19, 56);
  font-size: 30px;
}
</style>
