<template>
  <main>
    <navbar />
    <div class="main">
    <div class="go-back">
      <go-back />
    </div>

    <div class="settings" v-if="settings === true">
      <div class="header">Settings</div>
      <el-menu class="content">
        <el-menu-item class="item" @click="req('vendor-requirement')" v-if="this.$store.state.userInfo.userType === 'business'">
          <span>Vendor Requirements</span>
          <i class="el-icon-arrow-right"></i>
        </el-menu-item>
        <el-menu-item class="item" @click="req('documents')">
          <span>My Documents</span>
          <i class="el-icon-arrow-right"></i>
        </el-menu-item>
      </el-menu>
    </div>
    <div class="setting" v-if="setting === true">

      <div v-if="state == 'vendor-requirement'">
        <vendorrequirements />
      </div>
      <div v-if="state == 'documents'"><documents /></div>

      <div class="text" v-if="state == ''">
        <div class="bold">You have not selected a setting</div>Click setting to work on
      </div>
    </div>

    </div>
  </main>
</template>
<script>
import navbar from '../components/vendornavbar.vue'
import vendorrequirements from "../components/vendorrequirements.vue";
import documents from "../components/documents.vue";
export default {
  data: () => {
    return {
      state: '',
      setting: true,
      settings: true
    };
  },
  beforeCreate() {},
  components: {
    vendorrequirements,
    documents,
    navbar
  },
  methods: {
    req(view) {
      if (screen.width < 600) {
        this.$router.push(`/${view}`);
      } else {
        this.state = view;
        this.empty = false;
      }
    }
  }
};
</script>
<style scoped>
.main {
  display: flex;
}
.settings {
  width: 350px;
  padding-top: 20px;
  margin-left: 0px;
  min-height: 90vh;
  margin-left: 10px;
  border-right: 1px solid #eee;
}
* {
  font-family: "Poppins", sans-serif;
}
.header {
  font-size: 24px;
  padding-bottom: 5px;
}
.go-back {
  margin-left: 10px;
  margin-top: 11px;
}
.el-menu {
  border-right: transparent;
}
.content {
}
.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* border-bottom: 1px solid #eee; */
}
.setting {
  height: 92vh;
  width: 1000px;
}
.text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 90vh;
}
@media screen and (max-width: 600px) {
  .settings {
    width: 100vw !important;
  }
  .el-menu {
    width: 100% !important;
  }
  .setting {
    display: none;
  }
  .text {
    display: none;
  }
}
</style>
