<template>
  <div class="inventory">
    <div class="tablediv">
      <div class="justify">
        <div style="display: flex; ">
          <el-button type="text" @click="goBack()" class="btn">
            <i class="el-icon-back"></i>
          </el-button>
          <h2>{{ $format(this.$store.state.addVendorName)}}</h2>
        </div>
        <div>
          <el-button class="greenbtn" round @click="invite()" v-loading="loaded" v-if="this.$store.state.userInfo !== null">Invite vendor</el-button>
        </div>
      </div>
      <div v-if="inventory === null || inventory.length === 0" class="emptytextdiv ">
        This Vendor has no Product in his inventory
      </div>
      <div class="cards" >
        <div v-for="product in inventory" :key="product.id">
          <el-card class="card" :body-style="{ padding: '0px' }">
            <div class="height">
              <img src="https://via.placeholder.com/400.png?text=Product+Image" placeholder />
            </div>
            <div detaildiv>
              <div class="product">{{ product.name }}</div>
              <div class="details">&#8358; {{ product.price }}</div>
            </div>
          </el-card>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
[placeholder] {
  object-fit: cover;
  object-position: 50%;
  height: 250px;
  width: 100%;
}
.tablediv {
  width: 100%;
}
.justify {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.emptytextdiv {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: auto;
}
[detaildiv] {
  font-size: 15px;
  padding: 0 10px 10px;
}
.product {
  font-weight: bold;
  font-size: 20px;
}
.cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}
.inventory {
  margin: auto;
  background-color: #ffffff;
  width: 90%;
  padding-top: 25px;
  display: flex;
}
.el-card {
  height: fit-content;
  margin: 15px;
  border-radius: 15px;
}
.details {
  word-wrap: break-word;
  padding: 3px;
  font-size: 15px;
}
.btn {
  color: rgb(19, 19, 56);
  font-size: 30px;
}
</style>
<script>
import axios from "axios";
import { mapState } from "vuex";
export default {
  data() {
    return {
      loading: true,
      loaded: false
      //   vendor:
    };
  },
  beforeCreate() {
    this.$store.dispatch("inventory");
    const header = this.$store.state.jwt;
    axios.get(
      this.$store.state.apiUrl +
        `/api/v1/details/vendor/${this.$store.state.addVendorId}`,
      header
    );
  },
  computed: {
    ...mapState(["inventory"])
  },
  methods: {
    goBack() {
      window.history.back();
    },
    invite() {
      this.loaded = true;
      const config = {
        headers: this.$store.state.jwt
      };
      var email = this.$store.state.addVendorEmail;
      const userData = { email };

      axios
        .post(
          this.$store.state.apiUrl + "/api/v1/accounts/invite-vendor",
          userData,
          config
        )
        .then(response => {
          this.$store.dispatch("getVendorsList");
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: response.data.message
          });
          Toast.fire({
            type: "success",
            title: response.data.message
          });
          this.loaded = false;
        })
        .catch(err => {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "error",
            title: err
          });
          Toast.fire({
            type: "error",
            title: err.response.data.message
          });
          this.loaded = false;
        });
    }
  }
};
</script>
