    <template>
  <div>
      <history-go-back/>
    <el-card class="card" v-if="viewRfp !== null">
      <h2 style="text-align: center">{{$format(viewRfp.title)}}</h2>
      <div style="text-align: center">
        from:
        <b>{{$format(this.$store.state.viewRfpSender.business.name)}}</b>
      </div>
      <p class="desc">
        {{$formatText(viewRfp.description)}}
      </p>
      <p class="desc">
       Location: {{$format(viewRfp.location)}}
      </p>
      <p class="desc">
        Quantity: {{viewRfp.quantity}}
      </p>
      <p class="desc">
        Date Sent: {{
          new Date(viewRfp.createdAt).getDate() +
            ' - ' + (new Date(viewRfp.createdAt).getMonth() + 1) +
            ' - ' + new Date(viewRfp.createdAt).getFullYear()}}
      </p>
      <p class="desc">
        Deadline: {{$formatText(viewRfp.deadline)}}
      </p>
      <el-button class="sendQuotebtn" @click="sendQuote(viewRfp)">Send Quote</el-button>
    </el-card>
  </div>
</template>
<style scoped>
.card {
  width: 490px;
  margin: auto;
  margin-top: 50px;
}
.sendQuotebtn{
    margin: auto;
    background-color: rgb(19, 19, 56);
    color: #ffffff;
}
</style>
<script>
import {mapState} from 'vuex'
export default {
  computed: {
    ...mapState(['viewRfp'])
  },
    methods: {
    sendQuote(id) {
      this.$store.state.rfpId = id;
      this.$router.push("/quoatation");
    },
    }
}
</script>