import Vue from "vue";
import VueRouter from "vue-router";
import exploreservices from "../views/exploreservices.vue";
import settings from "../views/settings.vue";
import Home from "../views/Home.vue";
// registration Routes
import store from "@/store/index.js";
import registrationLayout from "../layouts/registrationLayout.vue";
import registrations from "../views/registrations/registrations";
import login from "../views/registrations/login";
import emailauth from "../views/registrations/emailauth.vue";
import invitevendor from "../views/registrations/invitevendor.vue";
import profile from "../components/profile.vue";
import inventory from "../views/inventory.vue";
import addinventory from "../views/addinventory.vue";

// message 
import messagelayout from '../layouts/message.vue'
import messages from '../views/messages/messages.vue'
import message from '../views/messages/message.vue'

// components
import setrequirements from "../components/vendorrequirements.vue";

// user Layout
import userLayout from "../layouts/userLayout.vue";
import userdashboard from "../views/user/userdashboard.vue";
import userrfq from "../views/user/userrfq.vue";

// business routes
import businessLayout from "../layouts/businessLayout.vue";
import businessrfqlayout from "../layouts/businessrfqlayout.vue";
import vendorProfileLayout from "../layouts/vendors.vue";
import businessprofilelayout from "../layouts/businessprofilelayout.vue";
import admindashboard from "../views/business/admindashboard.vue";
import viewvendorinventory from "../views/business/viewvendorinventory.vue";
import search from "../views/business/search.vue";
import rfq from "../views/business/rfq.vue";
import sentrfqs from "../views/business/sentrfqs.vue";
import viewvendor from "../views/business/viewvendor.vue";
import adminmessages from "../views/business/messages.vue";
import category from "../views/business/category.vue";
import adminmessage from "../views/business/adminmessage.vue";
import adminviewquotes from "../views/business/adminviewquotes.vue";
import admins from "../views/business/admins.vue";
import vendors from "../views/business/vendors.vue";
import acceptedbids from "../views/business/acceptedbids.vue";
import vendorcategories from "../views/business/vendorcategories.vue";

// vendor routes
import vendorLayout from "../layouts/vendorLayout.vue";
import businessprofile from "../layouts/businessprofile.vue";
import vendordashboard from "../views/vendor/vendordashboard.vue";
import quoatation from "../views/vendor/quotes.vue";
import businesses from "../views/vendor/businesses.vue";
import personalrfqs from "../views/vendor/personalrfqs.vue";
import businessrfqs from "../views/vendor/businessrfps.vue";
import rfpdetails from "../views/vendor/rfpdetails.vue";
import publicrfq from "../views/vendor/publicrfq.vue";
import pendingbusiness from "../views/vendor/pendingbusiness.vue";
import explore from "../views/vendor/explore.vue";
import searchBusiness from "../views/vendor/searchBusiness.vue";
import completerequirement from "../views/vendor/completerequirement.vue";
import applytobusiness from "../views/vendor/applytobusiness.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "*",
    redirect: "/"
  },
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/edit-profile",
    component: profile
  },
  {
    path: "/view-vendor-inventory",
    component: viewvendorinventory,
    beforeEnter: (to, from, next) => {
      const { addVendorEmail } = store.state;
      if (addVendorEmail !== null) {
        next();
      } else {
        next("/admin-dashboard");
      }
    }
  },
  {
    path: '/messagelayout',
    component: messagelayout,
    children: [
      {
        path: '/messages',
        component: messages
      },
      {
        path: '/message',
        component: message
      }
    ]
  },
  {
    path: '/explore-services',
    component: exploreservices
  },
  {
    path: "/vendor-requirement",
    component: setrequirements
  },
  {
    path: "/registration",
    component: registrationLayout,
    children: [
      {
        path: "/email-auth",
        component: emailauth
      },
      {
        path: "/register",
        component: registrations
      },
      {
        path: "/login",
        component: login
      },
      {
        path: "/invite-vendor",
        component: invitevendor
      }
    ]
  },

  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue")
  },
  {
    path: "/inventory",
    component: inventory
  },
  {
    path: "/add-inventory",
    component: addinventory
  },
  {
    path: "/settings",
    component: settings
  },
  {
    path: "/userlayout",
    component: userLayout,
    children: [
      {
        path: "/user-dashboard",
        component: userdashboard
      },
      {
        path: "/user-rfq",
        component: userrfq
      }
    ],
    beforeEnter: (to, from, next) => {
      const { userInfo } = store.state;
      if (userInfo && userInfo.userType === "regular") {
        next();
      } else {
        next("/login");
      }
    }
  },
  {
    path: "/businesslayout",
    component: businessLayout,
    children: [
      {
        path: "/admin-dashboard",
        component: admindashboard
      },
      {
        path: "/search",
        component: search,
      },
      {
        path: "/rfq",
        component: rfq
      },
      {
        path: "/view-vendor",
        component: viewvendor
      },
      {
        path: "/businessprofilelayout",
        component: businessprofilelayout,
        children: [
          {
            path: "/admins",
            component: admins
          },
          {
            path: "/vendorProfileLayout",
            component: vendorProfileLayout,
            children: [
              {
                path: "/vendors",
                component: vendors
              },
              {
                path: "/vendor-categories",
                component: vendorcategories
              },
              {
                path: "/vendor-category",
                component: category,
                beforeEnter: (to, from, next) => {
                  const { category } = store.state;
                  if (category !== null) {
                    next();
                  } else {
                    next("/vendor-categories");
                  }
                }
              }
            ]
          }
        ]
      },

      {
        path: "/rfqlayout",
        component: businessrfqlayout,
        children: [
          {
            path: "/view-quotes",
            component: adminviewquotes
          },
          {
            path: "/sent-rfqs",
            component: sentrfqs
          },
          {
            path: "/accepted-bids",
            component: acceptedbids
          }
        ]
      },
      {
        path: "/admin-messages",
        component: adminmessages
      },
      {
        path: "/admin-message",
        component: adminmessage
      }
    ],
    beforeEnter: (to, from, next) => {
      const { userInfo } = store.state;

      if (userInfo && userInfo.userType === "business" ||userInfo &&  userInfo.userType ==='regular') {
        next();
      } else {
        next("/login");
      }
    }
  },
  {
    path: "/vendorlayout",
    component: vendorLayout,
    children: [
      {
        path: "/vendor-dashboard",
        component: vendordashboard
      },
      {
        path: "/explore",
        component: explore
      },
      {
        path: '/public-rfp-view',
        component: publicrfq
      },
      {
        path: "/rfp-details",
        component: rfpdetails,
        beforeEnter: (to, from, next) => {
          if (store.state.viewRfp !== null) {
            next()
          } else {
            next('/explore')
          }
        }
      },
      {
        path: "/search-business",
        component: searchBusiness
      },
      {
        path: "/private-rfps",
        component: personalrfqs
      },
      {
        path: "/business-rfps",
        component: businessrfqs
      },
      {
        path: "/apply-to-business",
        component: applytobusiness
      },
      {
        path: "/quoatation",
        component: quoatation,
        beforeEnter: (to, from, next) => {
          const { rfpId } = store.state;
          if (rfpId !== null) {
            next();
          } else {
            next("/explore");
          }
        }
      },
      {
        path: "/review-application",
        component: completerequirement,
        beforeEnter: (to, from, next) => {
          const { pendingBusinessDetails } = store.state;
          if (pendingBusinessDetails !== null) {
            next();
          } else {
            next("/pending-businesses");
          }
        }
      },
      {
        path: "/business",
        component: businessprofile,
        children: [
          {
            path: "/businesses",
            component: businesses
          },
          {
            path: "/pending-businesses",
            component: pendingbusiness
          },
        ]
      },
    ],
    // beforeEnter: (to, from, next) => {
    //   const { userInfo } = store.state;

    //   if (userInfo && (userInfo.userType === "vendor" || "business")) {
    //     next();
    //   } else {
    //     next("/login");
    //   }
    // }
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
