<template>
  <nav>
    <div id="modal" v-loading="loading" v-if="changeRoute === true"></div>
    <div class="vendor">
      <el-button class="title" type="text" @click="dashboard()">Vendor Alliance</el-button>
    </div>
    <div class="div">
      <router-link
        to="/search-business"
        class="router-link"
        v-if="this.$store.state.userInfo.userType === 'vendor'"
      >
        <i class="el-icon-search"></i>
      </router-link>
      <router-link
        to="/search"
        class="router-link"
        v-if="this.$store.state.userInfo.userType === 'business'"
      >
        <i class="el-icon-search"></i>
      </router-link>
      <el-dropdown trigger="click">
        <el-button @click="notifications()" circle class="notificationbtn">
          <i class="el-icon-message-solid"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown" class="el-menu">
          <el-container v-if="this.$store.state.accountNots.length === 0" class="not">
            No Notifications yet. Create Notifications by performing
            operations
          </el-container>
          <div v-for="notification in accountNots" :key="notification.id">
            <el-dropdown-item class="not">
                <i class="el-icon-user-solid" v-if="notification.type === 'accounts'"></i>
                <div>{{ notification.message }}</div>
            </el-dropdown-item>
          </div>
        </el-dropdown-menu>
      </el-dropdown>
      <router-link to="/messages" class="router-link">
        <i class="el-icon-message"></i>
      </router-link>

      <router-link
        to="/settings"
        class="router-link"
        style="width: 20px; position: relative; margin-right: 15px; margin-left: 15px;"
      >
        <i class="el-icon-setting"></i>
        <span
          class="reddot"
          v-if="this.$store.state.empty === true && this.$store.state.userInfo.userType === 'business'"
        >.</span>
      </router-link>
      <el-dropdown trigger="click">
        <el-avatar :src="this.$store.state.userInfo.avatar"></el-avatar>
        <el-dropdown-menu id="menu">
          <router-link to="/edit-profile" class="link">
            <el-dropdown-item>Profile</el-dropdown-item>
          </router-link>
          <el-button @click="logOut()" class="logoutbtn">Log Out</el-button>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </nav>
</template>
<script>
import { mapState } from "vuex";
import pubnub from "../helpers/references.js";
export default {
  data: () => {
    return {
      name: "",
      loading: true,
      changeRoute: false
    };
  },
  computed: {
    ...mapState(["accountNots"])
  },
  beforeCreate() {
    this.$store.dispatch("getNotifications");
    pubnub.addListener({
      message: notification => {
        this.$store.commit("updateAccountNots", [notification.message]);
        
      }
    });
    pubnub.subscribe({
      channels: [`notifications-${this.$store.state.userInfo.id}`]
    });
  },
  methods: {
    logOut() {
      this.$store.dispatch("logout");
      this.$router.push("/login");
    },
    notifications() {},
    dashboard() {
      if (this.$store.state.userInfo.userType === "business") {
        this.$router.push("admin-dashboard");
      } else if (this.$store.state.userInfo.userType === "vendor") {
        this.$router.push("/vendor-dashboard");
      } else if (this.$store.state.userInfo.userType === "regular") {
        this.$router.push("/user-dashboard");
      }
    }
  }
};
</script>
<style scoped>
nav {
  background-color: rgb(19, 19, 56);
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 45px;
  padding-left: 40px;
  /* position: fixed; */
}
.reddot {
  background-color: red !important;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  position: absolute;
  top: 0px;
  right: 0px;
}
.not {
  padding: 15px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #eee;
}
.searchvendorbtn {
  display: flex;
  align-items: center;
}

.title {
  font-family: "Poppins", sans-serif;
  color: #ffffff;
  width: 150px;
  display: flex;
  text-decoration: none;
  align-items: center;
}
.searchItem {
  display: flex;
  justify-content: space-between;
  /* border-bottom: 1px solid #dcdfe6; */
  align-items: center;
  padding: 7px;
}
.searchicon {
  background-color: transparent;
}
#menu {
  width: 500px !important;
}
#input {
  -webkit-appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 30px;
  line-height: 40px;
  outline: 0;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 300px;
}
.div {
  margin-right: 40px;
  display: flex;
  align-items: center;
}
i {
  font-size: 15px;
}
.el-avatar--circle {
  width: 25px;
  height: 25px;
}
.el-dropdown-item {
  padding-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #eee;
}
.el-menu {
  width: 600px;
  /* overflow-y: scroll !important; */
}
.vendor {
  display: flex;
}
.router-link{
  color: white;
}
.findingresults {
  border-top: 1px solid #dcdfe6;
  display: flex;
  justify-content: center;
  text-align: center;
}
.logoutbtn {
  border: transparent;
}
</style>
