<template>
  <div class="body">
    <div id="modal" v-loading="pageLoading" v-if="pageLoading === true"></div>
    <div id="modal" v-if="modal === true">
      <el-card class="modalcard" v-if="modal === true && input === false  && archives === false">
        <div class="circleicon">
          <i class="el-icon-document-add"></i>
        </div>
        <div
          style="margin-top: 15px; font-weight: bold; text-align: center"
        >Store document in Archives</div>
        <div
          style="font-size: 14px; margin-top: 5px;"
        >Documents stored in archives can be used for future referencing</div>
        <div class="savebtns">
          <el-button class="greenbtn" @click="input = true">Save</el-button>
          <el-button @click="cancel()">Dont Save</el-button>
        </div>
      </el-card>
      <el-card class="modalcard" v-if="input === true" style="padding-top: 0px">
        <div class="modalbuttondiv">
          <el-button class="modalclosebtn" @click="cancel()">
            <i class="el-icon-close"></i>
          </el-button>
        </div>
        <el-form>
          <el-form-item label="Document Name">
            <el-input v-model="name"></el-input>
          </el-form-item>
          <el-button
            class="greenbtn"
            style="width: 100%"
            @click="archive()"
            v-loading="docAddLoading"
          >Add to Archives</el-button>
        </el-form>
      </el-card>
      <el-card class="modalcard" v-if="documents.length !== 0 && archives === true">
        <div class="modalbuttondiv">
          <el-button class="modalclosebtn" @click="modal = false; archives = false"><i class="el-icon-close"></i></el-button>
        </div>
        <el-table :data="documents" style="width: 100%">
          <el-table-column label="Name" width="180">
            <template slot-scope="scope">
              <el-popover trigger="hover" placement="top">
                <p>Name: {{ scope.row.name }}</p>
                <div slot="reference" class="name-wrapper">
                  <el-tag size="medium">{{ scope.row.name }}</el-tag>
                </div>
              </el-popover>
            </template>
          </el-table-column>
          <el-table-column label="Operations">
            <template slot-scope="scope">
              <el-button size="mini" class="greenbtn" @click="doc(null, null, scope.row)">Select</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
    </div>
    <div class="orgnamecontainer">
      <history-go-back class="btn" />
      <div style=" display: flex; align-items: center; margin-left: 20px;">
        <el-avatar
          shape="square"
          class="avatar"
          :src="this.$store.state.pendingBusinessDetailsAvatar"
        ></el-avatar>
        <h2 class="heading">{{ $format(pendingBusinessDetails.name) }}</h2>
      </div>
    </div>
    <div
      style="text-align: center; font-weight: bold; margin: 10px;"
      v-if="this.$store.state.fulfillmentsresponse.rejected === true && this.$store.state.fulfillmentsresponse.rejectComment === undefined"
    >'Your application was not accepted '</div>
    <div
      style="text-align: center; font-weight: bold; margin: 10px;"
      v-if="this.$store.state.fulfillmentsresponse.rejected === true && this.$store.state.fulfillmentsresponse.rejectComment !== undefined"
    >{{ `Your requirements were rejected for the following reasons:  ` +this.$store.state.fulfillmentsresponse.rejectComment}}</div>
    <div
      style="text-align: center; font-weight: bold; margin: 10px;"
      v-if="this.$store.state.fulfillmentsresponse.rejected === false || !this.$store.state.fulfillmentsresponse.rejected  "
    >{{$format(pendingBusinessDetails.name)}} has invited you to be a vendor in his business. Please submit accurate and complete information including any attachments that have been requested by {{$format(pendingBusinessDetails.name)}} to join his business.</div>
    <div class="buisness">
      <div class="admincontainer">
        <div class="admincontainerheader">
          <b>STATUTORY</b>
        </div>
        <div
          class="adminempty"
          v-if=" !this.$store.state.pendingBusinessDetails.requirements.statutory "
        >
          <div class="adminemptyheader">This business has no statutory requirement set</div>
        </div>
        <div
          class="admins"
          v-for="requirement in pendingBusinessDetails.requirements.statutory"
          :key="requirement.id"
        >
          <div class="admin">
            <div class="creds">
              <div style="display: flex; align-items: center; font-size: 25px">
                <i class="el-icon-user"></i>
              </div>
              <h3 class="adminname">{{ $format(requirement.name) }}</h3>
            </div>
            <div style="display: flex; flex-direction: column">
              <span style="font-size: 12px; padding-bottom: 5px">Do you meet these requirement?</span>
              <div class="d-flex">
                <div>
                  <el-radio
                    v-model="form[requirement.id]"
                    label="1"
                    @change="fulReq(form[requirement.id], requirement.id)"
                  >Yes</el-radio>
                  <el-radio
                    v-model="form[requirement.id]"
                    label="0"
                    @change="fulReq(form[requirement.id], requirement.id)"
                  >No</el-radio>
                </div>
                <div :id="requirement.id" style="display: none;">
                  <i class="el-icon-loading"></i>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="admincontainer" style="margin-bottom: 50px">
        <div class="admincontainerheader">
          <b>DOCUMENT</b>
        </div>
        <div
          class="adminempty"
          v-if=" !this.$store.state.pendingBusinessDetails.requirements.statutory "
        >
          <div class="adminemptyheader">This business has no document requirement set</div>
        </div>
        <div
          class="admins"
          v-for="requirement in pendingBusinessDetails.requirements.document"
          :key="requirement.id"
        >
          <div
            class="admin"
            v-if="requirement.length === 0"
          >This buisness has no Document requirement.</div>
          <div class="admin">
            <div class="creds">
              <div style="display: flex; align-items: center; font-size: 25px">
                <i class="el-icon-document"></i>
              </div>
              <h3 class="adminname">{{ requirement.name }}</h3>
            </div>
            <div>
              <div
                class="accepted"
                v-if="
                  existingFulfillments.statutory[requirement.id] &&
                    existingFulfillments.statutory[requirement.id].accepted ===
                      true
                "
              >
                <i class="el-icon-check"></i> ACCEPTED
              </div>
              <div v-else>
                <div
                  v-if="
                    existingFulfillments.statutory[requirement.id] &&
                      existingFulfillments.statutory[requirement.id]
                        .accepted === false
                  "
                >
                  <div class="rejected">
                    <i class="el-icon-check"></i> REJECTED
                  </div>
                </div>
                <div style="display: flex;">
                  <el-button @click="trigger(requirement.id)" class="greenbtn">Upload</el-button>
                  <el-button @click="modal = true;archives = true;id = requirement.id">My Documents</el-button>
                  <input
                    type="file"
                    :ref="requirement.id"
                    class="file"
                    @change="pdfCheck(requirement.id)"
                    style="display: none"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { mapState } from "vuex";
import store from "../../store";
import { post } from "../../helpers/index.js";
export default {
  name: "complete-requirement",
  data: () => {
    return {
      id: null,
      archives: false,
      loading: true,
      pageLoading: false,
      modal: false,
      input: false,
      name: null,
      documentToUpload: null,
      docAddLoading: false,
      form: (() => {
        const fs = store.state.existingFulfillments.statutory;
        const form = {};
        for (const i in fs) {
          form[i] = fs[i].value === true ? 1 : 2;
        }
        return form;
      })()
    };
  },
  mounted() {
    this.$store.dispatch("getDocuments");
  },
  computed: {
    ...mapState(["pendingBusinessDetails", "existingFulfillments", "documents"])
  },
  methods: {
    cancel() {
      (this.modal = false), (this.input = false);
    },
    archive() {
      this.docAddLoading = true;
      let data = new FormData();
      data.append("document", this.documentToUpload);
      data.append("name", this.name);
      let vueApp = this;
      post(this.$store.state.apiUrl + "/api/v1/archives", data, vueApp, () => {
        this.modal = false;
        this.input = false;
        this.docAddLoading = false;
      });
    },
    trigger(id) {
      this.$refs[id][0].click();
    },
    fulReq(option, id) {
      let loader = document.getElementById(id);
      loader.style.display = "block";
      const formData = new FormData();
      formData.append(
        "businessId",
        this.$store.state.pendingBusinessDetails.id
      );
      formData.append("type", "statutory");
      formData.append("value", option);
      formData.append("id", id);
      const headers = this.$store.state.jwt;
      axios
        .post(
          this.$store.state.apiUrl + "/api/v1/vendors/fulfill-requirements",
          formData,
          {
            headers: {
              ...headers,
              "Content-Type": "multipart/form-data"
            }
          }
        )
        .then(() => {
          this.$store.dispatch("getVendorsList");
        })
        .finally(() => {
          loader.style.display = "none";
        });
    },
    doc(id, documents, arc) {
      this.pageLoading = true;
      let api;
      const formData = new FormData();
      if (id === null) {
        this.modal = false;
        this.archives = false;
        api = `/api/v1/vendors/fulfill-requirements?existing=true`;
        formData.append("document", arc.link);
        formData.append("id", this.id);
      } else if (id !== null) {
        api = "/api/v1/vendors/fulfill-requirements";
        formData.append("document", documents);
        formData.append("id", id);
      }
      formData.append(
        "businessId",
        this.$store.state.pendingBusinessDetails.id
      );
      formData.append("type", "document");
     post(this.$store.state.apiUrl + api, formData, this,() => {
          this.pageLoading = false;
          if (id !== null) {
            this.modal = true;
          } else if (id === null) {
            this.archives = false;
            this.modal = false;
          }
        })
        .then()
    },
    clickFile() {
      document.querySelector(".file").click();
    },
    pdfCheck(id) {
      const file = this.$refs[id][0].files[0];
      if (file.type !== "application/pdf") {
        alert("Only PDF files are acceptable");
      } else {
        this.documentToUpload = file;
        this.doc(id, file);
      }
    }
  }
};
</script>
<style scoped>
.heading {
  margin-left: 10px;
}
.savebtns {
  padding: 15px;
  width: 100%;
  display: flex;
  justify-content: center;
  padding-left: 0%;
}
.accepted,
#load {
  margin-left: 5px;
  display: none;
}
.circleicon {
  border: 1px solid rgb(69, 218, 69);
  color: rgb(69, 218, 69);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  margin: auto;
  font-size: 25px;
}
.rejected {
  font-weight: bold;
}
.accepted {
  color: green;
}
.rejected {
  color: red;
}
.btn {
  font-size: 30px;

  margin-left: 160px;
  color: rgb(19, 19, 56);
}
</style>
