<template>
  <div class="body">
    <addvendor />
    <div class="accountsheader">
      <el-autocomplete placeholder="Please input">
        <i class="el-icon-search el-input__icon" slot="suffix"></i>
      </el-autocomplete>
      <el-button class="greenbtn" @click="openModal()">Add Vendor to Category</el-button>
    </div>
    <div class="container">
      <div class="containerheader">
        <b>{{$format(this.$store.state.category)}}</b>
      </div>
      <div class="adminempty" v-if="catVendors.length === 0">
        <div class="adminemptyheader">No Vendors In this cateory yet</div>
        <el-button class="adminemptybutton emptybtn" @click="openModal()">Add Vendor from list of vendors</el-button>
      </div>

      <div v-for="vendor in catVendors" :key="vendor.id">
        <div class="containercontent">
          <div class="creds">
            <el-avatar shape="square"></el-avatar>
            <h3 class="containername">{{ $format(vendor.name)}}</h3>
          </div>
          <div class="d-flex center">
            <!-- <div>
              <el-button class="addvendor" @click="viewCategory(vendor)" style="margin: 5px">View</el-button>
            </div>-->
            <div>
              <el-button class="delbtn" @click="remove(vendor)">Remove</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import addvendor from "../../components/addvendor.vue";
import { mapState } from "vuex";
export default {
  data: () => {
    return {
      card: false
    };
  },
  computed: {
    ...mapState(["catVendors"])
  },
  components: { addvendor },
  methods: {
    openModal() {
      this.$store.dispatch("getUnCatVendors", this.$store.state.category);
      this.$store.state.card = true;
    },
    closeModal() {
      this.$store.state.card = false;
    },
    addVendor(category) {
      this.$store.state.chosenCat = category;
      this.$store.state.card = true;
    },
    makeVendor() {
      this.loading = true;
      const modal = document.getElementById("modal");
      const config = {
        headers: this.$store.state.jwt
      };
      axios
        .post(this.apiUrl, this.category, config)
        .then(response => {
          this.loading = false;
          this.$store.dispatch("getVendorsCategory");
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: response.data.message
          });
          Toast.fire({
            type: "success",
            title: response.data.message
          });
          this.loading = false;
          modal.style.display = "none";
        })
        .catch(err => {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "error",
            title: err
          });
          Toast.fire({
            type: "error",
            title: err.response.data.message
          });
          modal.style.display = "none";
          this.loading = false;
        });
    },
    remove(vendor) {
      const headers = this.$store.state.jwt;
      const data = {
        email: vendor.email,
        category: vendor.business_category
      };
      axios
        .post(
          this.$store.state.apiUrl +
            "/api/v1/accounts/business/remove-vendor-from-category",
          data,
          { headers }
        )
        .then(result => {
          this.$store.state.categories.splice(
            this.$store.state.categories.indexOf("category"),
            1
          );
          this.$store.dispatch("getVendorsList", this.$store.state.category);
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: result.data.message
          });
          Toast.fire({
            type: "success",
            title: result.data.message
          });
        })
        .catch(err => {
          this.$store.dispatch("swalErr", err.response.data);
        });
    },
    viewCategory(cat) {
      this.$store.dispatch("getVendorsList", cat);
      this.$router.push("/vendor-category");
    }
  }
};
</script>
