<template>
  <el-form ref="form" form>
    <el-form-item class="item">
      <el-input v-model="data.name" placeholder="Full name"></el-input>
    </el-form-item>
    <el-form-item class="item">
      <el-input v-model="data.email" placeholder="Email"></el-input>
    </el-form-item>
    <el-form-item class="item">
      <vue-tel-input v-model="data.phone" v-bind="verify"></vue-tel-input>
    </el-form-item>
    <el-form-item class="item">
      <el-input
        v-model="data.password"
        show-password
        placeholder="Password"
      ></el-input>
    </el-form-item>
    <el-form-item class="item">
      <el-input
        v-model="cpassword"
        show-password
        placeholder="Confirm Password"
      ></el-input>
    </el-form-item>

    <el-form-item v-loading="loading" class="buttonpadding">
      <el-button class="btn" @click="submitForm()" v-if="loading === false">
        Register
      </el-button>
    </el-form-item>
  </el-form>
</template>
<script>
import auth from "../../auth/index.js";
export default {
  data: () => {
    return {
      loading: false,
      cpassword: null,
      verify: {
        validate: true,
        mode: "international",
        defaultCountry: "NG",
        disabledFetchingCountry: false,
        disabled: false,
        disabledFormatting: true,
        placeholder: "Enter a phone number",
        required: true,
        enabledCountryCode: false,
        enabledFlags: true,
        onlyCountries: [],
        ignoredCountries: [],
        autocomplete: "off",
        name: "telephone",
        maxLen: 17,
        wrapperClasses: "",
        inputClasses: "",
        dropdownOptions: {
          disabledDialCode: false
        },
        inputOptions: {
          showDialCode: false
        }
      },
      data: {
        name: null,
        email: null,
        phone: null,
        password: null,
        userType: "regular"
      }
    };
  },
  methods: {
    async submitForm() {
      const vueApp = this;
      this.loading = true;
      auth.validEmail(this.data.email);
      if (auth.validEmail(this.data.email)) {
        if (this.data.password === this.cpassword) {
          try {
            await auth.reg(this.data, vueApp);
          } catch (_) {
            this.loading = false;
          }
        } else {
          alert("Please try matching your two passwords");
          this.loading = false;
        }
      } else {
        alert("Incorrect Email");
        this.loading = false;
      }
    }
  }
};
</script>
<style scoped>
[form] {
  padding: 20px 30px;
}
.btn {
  width: 100%;
  color: #ffffff;
  background-color: rgb(19, 19, 56);
  border-radius: 10px;
}
@media screen and (max-width: 600px) {
  [form] {
    padding: 20px 10px;
  }
}
</style>
