<template>
  <div>
    <el-button type="text" @click="goBack()" class="btn">
      <i class="el-icon-back"></i>
    </el-button>
  </div>
</template>
<script>
export default {
  methods: {
    goBack() {
      var userInfo = this.$store.state.userInfo.userType
        if(userInfo === 'vendor') {
            this.$router.push('/vendor-dashboard' )
        }
        //  else if(userInfo === 'regular') {
        //     this.$router.push('/regular-dashboard' )
        // }
         else if(userInfo === 'business' || userInfo === 'regular') {
            this.$router.push('/admin-dashboard' )
        }
    }
  }
};
</script>
<style scoped>
.btn {
  font-size: 30px;
  color: rgb(19, 19, 56);
}
</style>
