const format = (str = ' ') => {
  str = str.toLowerCase()
  str = str.split(' ')
  for (var i = 0; i < str.length; i++) {
    str[i] = str[i].charAt(0).toUpperCase() + str[i].slice(1)
  }
  return str.join(' ')
}
const formatText = (str = ' ') => {
  str = str[0].toUpperCase() + str.slice(1)

  return str
}
const post = async (url, data, vueApp, callback = () => {}) => {
   let config = {
      headers: vueApp.$store.state.jwt
    };
  await axios
    .post(url, data, config).then(result => {
      const Toast = vueApp.$swal.mixin({
        toast: true,
        position: "top-end",
        showConfirmButton: false,
        timer: 5000,
        type: "success",
        title: result.data.message
      });
      Toast.fire({
        type: "success",
        title: result.data.message
      });
    }).catch(err => {
      const Toast = vueApp.$swal.mixin({
        toast: true,
        position: "top-end",
        showConfirmButton: false,
        timer: 5000,
        type: "error",
        title: err.response.data.message
      });
      Toast.fire({
        type: "error",
        title: err.response.data.message
      });
    }).finally(() => {
      callback();
    })
}
let openNotification = (message, type,vueApp,) => {
  const Toast = vueApp.$swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 5000,
    type: type,
    title: message
  });
  Toast.fire({
    type: "error",
    title: message
  });
}
export {
  format,
  formatText,
  post,
  openNotification
 }