<template>
  <div class="addvendorbody" v-if="this.$store.state.card === true">
    <el-card class="addvendorcard" v-loading="loading">
      <div class="btndiv">
        <el-button circle class="closebtn" @click="closeModal()">
          <i class="el-icon-close"></i>
        </el-button>
      </div>
      <span>Select Vendor to add</span>
      <div class="empty" v-if="this.$store.state.vendors.length === 0">
        This business has no vendor yet.
        <router-link to="/vendors">click to add vendor</router-link>
      </div>
      <el-menu v-for="vendor in unCatVendors" :key="vendor.id">
        <el-menu-item @click="addvendor(vendor.email)">{{
          vendor.name
        }}</el-menu-item>
      </el-menu>
    </el-card>
  </div>
</template>
<script>
import axios from "axios";
import { mapState } from "vuex";
export default {
  data: () => {
    return {
      loading: false
    };
  },
  computed: {
    ...mapState(["unCatVendors"])
  },
  beforeCreate() {},
  methods: {
    addvendor(email) {
      this.loading = true;
      const data = {
        email: email,
        category: this.$store.state.category
      };
      const config = {
        headers: this.$store.state.jwt
      };
      const apiUrl =
        this.$store.state.apiUrl +
        "/api/v1/accounts/business/add-vendor-to-category";
      axios
        .post(apiUrl, data, config)
        .then(response => {
          this.$store.dispatch("getVendorsList", this.$store.state.category);
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: response.data.message
          });
          Toast.fire({
            type: "success",
            title: response.data.message
          });
      this.loading = false;
          this.$store.state.card = false;
        })
        .catch(err => {
          this.$store.state.card = false;
          this.loading = false;
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "error",
            title: err
          });
          Toast.fire({
            type: "error",
            title: err.response.data.message
          });
      this.loading = false;
        });
    },
    closeModal() {
      this.$store.state.card = false;
    }
  }
};
</script>
<style scoped>
.addvendorbody {
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
  padding-top: 10%;
}
.addvendorcard {
  width: 400px;
  margin: auto;
}

.closebtn {
  background-color: transparent;
  border-color: transparent;
}
.btndiv {
  display: flex;
  justify-content: flex-end;
}
#select {
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: 0;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}
.addtocategory {
  width: 100%;
  color: #fff;
  background-color: #28a745;
  background-image: linear-gradient(-180deg, #34d058, #28a745 90%);
}
</style>
