<template>
  <div class="body">
    <div class="search">
      <div class="searchinput">
        <el-input type="text" placeholder="Search Bids" />
        <el-button class="searchbtn">Search</el-button>
      </div>
    </div>
    <div  v-if="acceptedQuotes.length === 0" style="text-align: center; line-height: 30px;padding: 40px;">
      <div style="font-size: 18px;font-weight: bold;">No quotes been accepted yet.</div>
      <div>Create <router-link to="/rfq">RFQ's</router-link> to recieve quotes </div>
      

    </div>
    <div v-if="acceptedQuotes.length !== 0">
      
    <div class="bids" v-for="quote in acceptedQuotes" :key="quote.id">
      <div class="bid">
        <el-button type="text" class="btn">
          <b>{{$format(quote.rfq.title)}}</b>
        </el-button>
        <span>{{$format(quote.vendor.name)}}</span>
      </div>
    </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { mapState } from "vuex";
export default {
  beforeCreate() {
    axios
      .get(this.$store.state.apiUrl + "/api/v1/business/quotes?flag=accepted", {
        headers: this.$store.state.jwt
      })
      .then(result => {
        this.$store.state.acceptedQuotes = result.data.quotes;
      });
  },
  computed: {
    ...mapState(["acceptedQuotes"])
  }
};
</script>
<style scoped>
.body {
  padding-left: 14%;
  margin-top: 0%;
  background-color: #ffffff;
}
.search {
  border-bottom: 1px solid #eee;
  padding-bottom: 1%;
  margin-right: 5%;
}
.searchinput {
  display: flex;
  justify-content: flex-start;
  margin-top: 2%;
  width: 50%;
}
.searchbtn {
  color: #fff;
  background-color: #28a745;
  background-image: linear-gradient(-180deg, #34d058, #28a745 90%);
}
.bids {
}
.bid {
  display: flex;
  flex-direction: column;
  text-align: start;
  padding: 2%;
  border-bottom: 1px solid #eee;
  margin-right: 5%;
}
.btn {
  display: flex;
  justify-self: flex-start;
  font-size: 25px;
}
</style>
