<template>
  <div class="body">
    <addvendor />
    <div class="accountsheader">
      <el-autocomplete placeholder="Please input">
        <i class="el-icon-search el-input__icon" slot="suffix"></i>
      </el-autocomplete>
      <el-button class="greenbtn" @click="openModal()">Add Categories</el-button>
      <div id="modal" v-if="card === true">
        <el-card class="modalcard" v-if="del === true">
          <div class="del-pop-up-btn">
            <i class="el-icon-delete"></i>
          </div>
          <div class="del-pop-up-header">delete vendor category</div>
          <div class="del-pop-up-text">Are you sure you want to delete this vendor category</div>
          <div style="display: flex; justify-content: center; margin-top: 4px">
            <el-button class="del-action-pop-up-btn" @click="remove()">Delete</el-button>
            <el-button @click="closeDelPopUp()">Dont Delete</el-button>
          </div>
        </el-card>
        <div v-if="addCategory === true">
          <el-card class="modalcard" v-loading="loading">
            <div class="modalbuttondiv">
              <el-button circle class="modalclosebtn" @click="closeModal()">
                <i class="el-icon-close"></i>
              </el-button>
            </div>
            <el-form ref="form" id="form">
              <el-form-item label="Category name" label-position="top">
                <el-input v-model="category.category"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button class="btn" @click="makeVendor()">Add Category</el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </div>
      </div>
    </div>
    <div class="container">
      <div class="containerheader">
        <b>Business Categories</b>
      </div>
      <div class="adminempty" v-if="this.$store.state.categories.length === 0">
        <div class="adminemptyheader">No Vendor categories yet</div>
        <el-button class="adminemptybutton emptybtn" @click="openModal()">Create vendor category</el-button>
      </div>
      <div class v-for="category in categories" :key="category">
        <div class="containercontent">
          <div class="creds">
            <el-avatar shape="square"></el-avatar>
            <h3 class="containername">{{category}}</h3>
          </div>
          <div class="d-flex center">
            <div>
              <el-button class="addvendor" @click="viewCategory(category)">View</el-button>
            </div>
            <div>
              <el-button class="delbtn" @click="delPopUp(category)">Delete</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import addvendor from "../../components/addvendor.vue";
import { mapState } from "vuex";
export default {
  data() {
    return {
      centerDialogVisible: false,
      apiUrl: this.$store.state.apiUrl + "/api/v1/accounts/business/category",
      loading: false,
      category: {
        category: null
      },
      card: false,
      addCategory: false,
      del: false,
      cat: null
    };
  },
  components: {
    addvendor
  },
  computed: {
    ...mapState(["categories"])
  },
  beforeCreate() {
    this.$store.dispatch("getVendorsCategory");
    this.$store.dispatch("getVendorsList");
    this.$store.dispatch("getUnCatVendors");
  },
  methods: {
    delPopUp(category) {
      this.del = true;
      this.card = true;
      this.cat = category;
    },
    closeDelPopUp() {
      this.del = false;
      this.card = false;
      this.cat = null;
    },
    openModal() {
      this.card = true;
      this.addCategory = true;
    },
    closeModal() {
      this.card = false;
      this.addCategory = false;
    },
    addVendor(category) {
      this.$store.state.chosenCat = category;
      this.$store.state.card = true;
    },
    makeVendor() {
      this.loading = true;
      const config = {
        headers: this.$store.state.jwt
      };
      axios
        .post(this.apiUrl, this.category, config)
        .then(response => {
          this.loading = false;
          this.$store.dispatch("getVendorsCategory");
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: response.data.message
          });
          Toast.fire({
            type: "success",
            title: response.data.message
          });
          this.loading = false;
          this.card = false;
          this.category.category = null;
          document.getElementById("form").reset();
        })
        .catch(err => {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "error",
            title: err
          });
          Toast.fire({
            type: "error",
            title: err.response.data.message
          });
          this.card = false;
          this.addCategory = false;
          this.loading = false;
          this.category.category = null;
        });
      document.getElementById("form").reset();
      this.card = false;
      this.addCategory = false;
      this.loading = false;
    },
    remove() {
      const headers = this.$store.state.jwt;
      const data = {
        category: this.cat
      };
      axios
        .delete(
          this.$store.state.apiUrl + "/api/v1/accounts/business/category",
          {
            data,
            headers
          }
        )
        .then(result => {
          this.$store.state.categories.splice(
            this.$store.state.categories.indexOf(this.cat),
            1
          );
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: result.data.message
          });
          Toast.fire({
            type: "success",
            title: result.data.message
          });
        });

      this.del = false;
      this.card = false;
    },
    viewCategory(cat) {
      this.$store.dispatch("getVendorsList", cat);
      this.$router.push("/vendor-category");
    }
  }
};
</script>
<style scoped>
.btn {
  width: 100%;
  background-color: #0066f5;
  color: #ffffff;
}
.addvendor {
  margin-right: 5px;
}
</style>
