<template>
  <div class="body">
    <div id="modal" v-if="del === true">
      <el-card class="modalcard">
        <div class="del-pop-up-btn">
          <i class="el-icon-delete"></i>
        </div>
        <div class="del-pop-up-header">delete RFP</div>
        <div class="del-pop-up-text">Are you sure you want to delete this RFP</div>
        <div style="display: flex; justify-content: center; margin-top: 4px">
          <el-button class="del-action-pop-up-btn" @click="delRfq()">Delete</el-button>
          <el-button @click="closeDelPopUp()">Dont Delete</el-button>
        </div>
      </el-card>
    </div>
    <div class="search">
      <div class="searchinput">
        <div id="input">
          <el-input type="text" placeholder="Search Bids" class="admininput" />
        </div>
        <el-button class="greenbtn" @click="rfq()">Upload RFP</el-button>
      </div>
    </div>
    <div
      class="adminempty rfqempty"
      v-if="this.$store.state.rfqList === null ||this.$store.state.rfqList.length === 0"
    >
      <div class="adminemptyheader">No Sent RFP's yet</div>
      <router-link to="/rfq">
        <el-button class="adminemptybutton emptybtn">Send RFP</el-button>
      </router-link>
    </div>
    <div class="bids" v-for="rfq in rfqList" :key="rfq.id">
      <div class="bid">
        <div class="titles">
          <el-button type="text" class="btn">
            <b>{{ rfq.title }}</b>
          </el-button>
          <div>{{ rfq.quantity }}</div>
        </div>
        <el-button class="delbtn" @click="dele(rfq)">
          <i class="el-icon-delete" id="del"></i>
          <i class="el-icon-loading" id="load"></i>
        </el-button>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import axios from "axios";
export default {
  data() {
    return {
      del: false,
      object: null
    };
  },
  beforeCreate() {
    this.$store.dispatch("rfqList");
  },
  computed: {
    ...mapState(["rfqList"])
  },
  methods: {
    dele(obj) {
      this.object = obj;
      this.del = true;
    },
    delRfq() {
      const load = document.getElementById("load");
      const del = document.getElementById("del");
      del.style.display = "none";
      load.style.display = "block";
      const headers = this.$store.state.jwt;
      const data = { id: this.object.id };
      axios
        .delete(this.$store.state.apiUrl + "/api/v1/rfqs", {
          data,
          headers
        })
        .then(response => {
          this.$store.state.rfqList.splice(
            this.$store.state.rfqList.indexOf(this.object),
            1
          );
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: response.data.message
          });
          Toast.fire({
            type: "success",
            title: response.data.message
          });
          del.style.display = "block";
          load.style.display = "none";
        });
      this.del = false;
    },
    rfq() {
      this.$router.push("/rfq");
    },
    closeDelPopUp(){
      this.del = false
    }
  }
};
</script>
<style scoped>
.body {
  padding-left: 14%;
  margin-top: 0%;
  background-color: #ffffff;
}
.search {
  border-bottom: 1px solid #eee;
  padding-bottom: 1%;
  margin-right: 5%;
}
.searchinput {
  display: flex;
  justify-content: space-between;
  margin-top: 2%;
}
.bid {
  display: flex;
  text-align: start;
  padding: 2%;
  border-bottom: 1px solid #eee;
  margin-right: 5%;
  justify-content: space-between;
}
.titles {
  width: 80%;
}
.rfqempty {
  border: 1px solid #eee;
  border-top: transparent;
  width: 91.5%;
}
.btn {
  display: flex;
  justify-self: flex-start;
  font-size: 25px;
  text-overflow: ellipsis;
}
.delbtn {
  background-color: red;
  width: 60px;
  height: 60px;
  color: #ffffff;
}
i {
  font-size: 20px;
}
#load {
  font-size: 20px;
  display: none;
}
@media screen and (max-width: 600px) {
  .body {
    padding: 2%;
    margin-top: 0%;
    background-color: #ffffff;
  }
  .search {
    border-bottom: 1px solid #eee;
    padding-bottom: 1%;
    margin-right: 0%;
  }
  .bids {
    margin-top: 5px;
  }
}
</style>
