<template>
  <div class="search">
    <div id="modal" v-loading="loading" v-if="loading === true"></div>
    <el-card shadow="never">
      <div style="display: flex; justify-content: center; margin-bottom: 25px">
        <bread-crumb name="You are now exploring vendors" class="text-align: center" />
      </div>
      <el-container style="display: flex; align-items: center">
        <history-go-back style="margin-right: 25px" />
        <el-select v-model="value" @change="search()" :placeholder=" options[0].label ">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <div style="width: 350px; margin-left: 10px">
          <el-input
            v-model="name"
            @input="search()"
            width="350"
            placeholder="Search by vendor, product or category"
          ></el-input>
        </div>
      </el-container>
    </el-card>

    <div class="vendorsearchcontainer">
      <div
        v-if="this.unSearched === true && this.$store.state.searchedBusiness.length === 0"
        class="empty"
      >Try searching based On category</div>
      <div
        v-if="this.empty === true"
        class="empty"
      >No match in this search category. Try other search categories :)</div>
      <div accordion v-for="business in searchedBusiness" :key="business.id">
        <div
          v-if="business.length === 0"
        >No match in this search category. Try other search categories :)</div>
        <div class="collapse">
          <el-button type="text" class="textbtn">{{ $format(business.name) }}</el-button>
          <div v-if="!business.oem">
            <div class="detailsnrate">
              <div>
                <div class="email">Email: {{$format(business.email) }}</div>
                <div class="email">Phone: {{ business.phone }}</div>
                <div class="email">Service Category: {{ $format(business.service_category) }}</div>
                <div class="email">Service Location: {{ $format(business.service_location ) }}</div>
              </div>
              <div>
                <el-rate disabled></el-rate>
              </div>
            </div>
            <div class="modalbuttondiv">
              <el-button @click="inventory(business.id, business)">View Inventory</el-button>
            </div>
          </div>
          <div v-if="business.oem">
            <div class="detailsnrate">
              <div>
                <div class="email">Name: {{ $format(business.vendor.name)}}</div>
                <div class="email">Email: {{ $format(business.vendor.email )}}</div>
                <div class="email">Phone: {{ business.vendor.phone }}</div>
                <div class="email">Product: {{ $format(business.name )}}</div>
                <div class="email">Price: {{ business.price }}</div>
                <div class="email">OEM: {{ $format(business.oem)}}</div>
              </div>
              <div>
                <el-rate v-model="value1" disabled></el-rate>
              </div>
            </div>
            <div class="modalbuttondiv">
              <el-button @click="inventory(business.vendor.id, business.vendor)">View Inventory</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { mapState } from "vuex";
export default {
  name: "search",
  data: () => {
    return {
      value: null,
      name: null,
      unSearched: true,
      empty: false,
      loading: false,
      options: [
        {
          value: "vendor",
          label: "Vendors"
        },
        {
          value: "products",
          label: "Products"
        },
        {
          value: "category",
          label: "Category"
        },
        {
          value: "oem",
          label: "OEM"
        },
        {
          value: "service",
          label: "Service"
        }
      ]
    };
  },
  computed: {
    ...mapState(["searchedBusiness"])
  },
  methods: {
    search() {
      this.unSearched = false;
      const headers = this.$store.state.jwt;
      const config = { headers };

      setTimeout(() => {
        if (this.value === "service") {
          const api =
            this.$store.state.apiUrl +
            `/api/v1/accounts/vendor-search?q=${this.name}&by=service`;
          axios
            .get(api, config)
            .then(result => {
              if (result.data.vendors.length === 0) {
                this.empty = true;
              } else {
                this.empty = false;
              }
              this.$store.dispatch("setSearchedBusiness", result.data.vendors);
            })
            .catch(() => {
              this.$store.dispatch("loader", false);
              this.loading = false;
            });
        }
        if (this.value === "vendor" || this.value === null) {
          const api =
            this.$store.state.apiUrl +
            `/api/v1/accounts/vendor-search?q=${this.name}&by=name`;
          axios
            .get(api, config)
            .then(result => {
              if (result.data.vendors.length === 0) {
                this.empty = true;
              } else {
                this.empty = false;
              }
              this.$store.dispatch("setSearchedBusiness", result.data.vendors);
            })
            .catch(() => {
              this.$store.dispatch("loader", false);
              this.loading = false;
            });
        } else if (this.value === "products") {
          axios
            .get(
              this.$store.state.apiUrl +
                `/api/v1/accounts/product-search?q=${this.name}`,
              config
            )
            .then(result => {
              if (result.data.products.length === 0) {
                this.empty = true;
              } else {
                this.empty = false;
              }
              this.$store.dispatch("setSearchedBusiness", result.data.products);
            });
        } else if (this.value === "category") {
          axios
            .get(
              this.$store.state.apiUrl +
                `/api/v1/accounts/vendor-search?q=${this.name}&by=service_category`,
              config
            )
            .then(result => {
              if (result.data.vendors.length === 0 && this.name !== null) {
                this.empty = true;
              } else {
                this.empty = false;
              }
              this.$store.dispatch("setSearchedBusiness", result.data.vendors);
            });
        } else if (this.value === "oem") {
          axios
            .get(
              this.$store.state.apiUrl +
                `/api/v1/accounts/product-search?q=${this.name}&by=oem`,
              config
            )
            .then(result => {
              if (result.data.products.length === 0) {
                this.empty = true;
              }
              this.$store.dispatch("setSearchedBusiness", result.data.products);
            });
        }
      }, 500);

      if (this.name === null || this.name.length === 0) {
        this.$store.dispatch("setSearchedBusiness", []);
      }
    },
    makeVendor(email) {
      const config = {
        headers: this.$store.state.jwt
      };
      const userData = { email };
      axios
        .post(
          this.$store.state.apiUrl + "/api/v1/accounts/invite-vendor",
          userData,
          config
        )
        .then(response => {
          this.$store.dispatch("getVendorsList");
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: response.data.message
          });
          Toast.fire({
            type: "success",
            title: response.data.message
          });
        })
        .catch(err => {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "error",
            title: err
          });
          Toast.fire({
            type: "error",
            title: err.response.data.message
          });
        });
    },
    vendorDetails(id) {
      const headers = this.$store.state.jwt;
      const config = { headers };
      axios
        .get(this.$store.state.apiUrl + `/api/v1/details/vendor/${id}`, config)
        .then(result => {
          this.$store.state.searchedVendor = result.data.vendor;
          this.$router.push("/view-vendor");
        });
    },
    inventory(id, email) {
      this.loading = true;
      this.$store.state.addVendorName = email.name;
      this.$store.dispatch("inventory", id);
      this.$store.state.addVendorEmail = email.email;
      this.$store.state.addVendorId = id;
      setTimeout(() => {
        this.loading = false;
        this.$router.push("/view-vendor-inventory");
      }, 3000);
      // const headers = this.$store.state.jwt
      // const config = { headers }
      // const data = {
      //   id: id
      // }
      //
    }
  }
};
</script>
<style scoped>
/* .search {
   display: flex; 
} */
.detailsnrate {
  display: flex;
  justify-content: space-between;
}
.empty {
  font-weight: bold !important;
  display: flex;
  justify-content: center;
  font-size: 30px !important;
  color: rgb(19, 19, 56);
}
.el-card {
  display: flex;
  flex-direction: column;
}
.collapse {
  font-size: 20px !important;
}
.title {
  font-size: 26px;
  font-weight: bold;
}
.searchItem {
  color: rgb(4, 97, 97);
}
.textbtn {
  font-size: 25px;
}
.searchitem {
  /* color: rgb(19, 19, 56); */
  font-size: 24px;
  font-weight: bold;
}
.vendorsearchcontainer {
  width: 900px;
  margin: auto;
  padding-top: 30px;
}
#el-collapse-head-2549 {
  font-size: 30px !important;
}
.el-collapse-item__header {
  font-size: 30px !important;
}
.addbtn {
  margin-top: 5px;
}
* {
  font-family: "Poppins", sans-serif !important;
}
.el-menu {
  width: 200px;
  padding-top: 30px;
  padding-bottom: 10px;
  margin-left: 40px;
  margin-top: 40px;
}
.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.namebtn {
  font-size: 22px;
}
.email {
  font-size: 13px;
  padding: 5px;
}
</style>
