<template>
  <div class="inventory">
    <div id="modal" v-if="del === true">
      <el-card class="modalcard">
        <div class="del-pop-up-btn">
          <i class="el-icon-delete"></i>
        </div>
        <div class="del-pop-up-header">delete RFP</div>
        <div class="del-pop-up-text">Are you sure you want to delete this RFP</div>
        <div style="display: flex; justify-content: center; margin-top: 4px">
          <el-button class="del-action-pop-up-btn" @click="deleteInventory() ">Delete</el-button>
          <el-button @click="closeDelPopUp()">Dont Delete</el-button>
        </div>
      </el-card>
    </div>
    <div class="flex" style="display: flex; justify-content:space-between;">
      <go-back />
      <div style="font-size: 20px; font-weight: bold">MY INVENTORY</div>
      <div>
        <el-button class="addbtn" round @click="add()">
          <i class="el-icon-plus"></i>Add Product
        </el-button>
      </div>
    </div>
    <div
      style="display: flex; justify-content: center; align-items: center; "
    >No product in the inventory</div>
    <div class="tablediv" v-if="this.$store.state.inventory.length !== 0">
      <el-table style="width: 100%" :data="inventory">
        <el-table-column type="expand">
          <template slot-scope="props">
            <p v-if="props.row.type === 'product'">Name of Item: {{ props.row.name }}</p>
            <p v-if="props.row.type === 'service'">Name of Service: {{ props.row.name }}</p>
            <p>Type: {{ props.row.type }}</p>
            <p v-if="props.row.type === 'product'">OEM of Item: {{ props.row.oem }}</p>
            <p v-if="props.row.type === 'product'">Description of item: {{ props.row.description }}</p>
            <p
              v-if="props.row.type === 'service'"
            >Description of Service: {{ props.row.description }}</p>
            <p>Price of Item: {{ props.row.price }}</p>
            <p v-if="props.row.type === 'product'">Minimum order quantity: {{ props.row.moq }}</p>
            <p v-if="props.row.type === 'product'">Stock Keeping Unit: {{ props.row.sku }}</p>
            <div class="buttons">
              <el-button @click="dele(props.row)" id="dlt">
                <span v-if="loading == false">Delete Product</span>
                <i class="el-icon-loading" v-if="loading == true"></i>
              </el-button>
              <!-- <el-button>Unavailable</el-button>
              <el-button>Available</el-button>-->
            </div>
          </template>
        </el-table-column>
        <el-table-column label="Name Of Item" prop="name"></el-table-column>
        <el-table-column label="Price" prop="price"></el-table-column>
        <el-table-column label="Type" prop="type"></el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { mapState } from "vuex";
export default {
  data() {
    return {
      loading: false,
      obj: null,
      del: false
    };
  },
  beforeCreate() {
    this.$store.dispatch("inventory");
  },
  computed: {
    ...mapState(["inventory"])
  },
  methods: {
    add() {
      this.$router.push("/add-inventory");
    },
    dele(obj) {
      this.obj = obj;
      this.del = true;
    },
    closeDelPopUp() {
      this.obj = null;
      this.del = false;
    },
    deleteInventory() {
      this.loading = true;
      const headers = this.$store.state.jwt;
      const data = {
        productId: this.obj.id
      };
      axios
        .delete(
          this.$store.state.apiUrl + "/api/v1/accounts/vendors/inventory",
          { data, headers }
        )
        .then(response => {
          this.$store.state.inventory.splice(
            this.$store.state.inventory.indexOf(this.obj),
            1
          );
          this.loading = false;
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "error"
          });
          Toast.fire({
            type: "success",
            title: response.data.message
          });
        })
        .catch(err => {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "error"
          });
          Toast.fire({
            type: "error",
            title: err.response.data.message
          });
          this.loading = false;
        });
      this.del = false;
    }
  }
};
</script>

<style scoped>
.inventory {
  margin: auto;
  background-color: #ffffff;
  width: 90%;
  padding-top: 25px;
}
.btndiv {
  text-align: end;
}
.addbtn {
  background-color: #0066f5;
  color: #ffffff;
}
.tablediv {
  width: 1200px;
}
#dlt {
  background-color: red;
  color: #ffffff;
}
</style>
