<template>
  <div class="rfp">
    <history-go-back />
    <div class="form">
      <div style="display: flex; justify-content: center;">
        <bread-crumb name="Post RFP" />
      </div>
      <el-form id="form">
        <el-form-item label="Title Of RFP" label-position="top">
          <el-input type="text" v-model="data.title"></el-input>
        </el-form-item>
        <el-form-item label="Description Of RFP" label-position="top">
          <el-input type="textarea" autosize placeholder="I need ......" v-model="data.description"></el-input>
        </el-form-item>
        <el-form-item label="Service or Business category">
          <select
            name="category"
            v-model="data.type"
            :placeholder=" type[0].label "
            id="select"
            @change="chooseCat()"
          >
            <option
              v-for="item in type"
              :key="item.value"
              :label="item.label"
              :value="item.value"
              id="option"
            ></option>
          </select>
        </el-form-item>
        <el-form-item label="Select Business category" v-if="data.type === 'business'">
          <select
            name="category"
            v-model="data.category"
            placeholder="Select busineess category "
            id="select"
            @change="chooseCat()"
          >
            <option v-for="item in categories" :key="item" :label="item" :value="item" id="option"></option>
          </select>
        </el-form-item>
        <el-form-item v-if="data.type === 'service'">
          <select name="catchoice" v-model="data.category" placeholder="Pick Category" id="select">
            <option
              v-for="option in cat"
              :key="option.name"
              :label="option.name"
              :value="option.name"
              id="option"
            ></option>
          </select>
        </el-form-item>
        <el-form-item v-if="data.type === 'single'">
          <el-select
            id="select"
            v-model="data.category"
            multiple
            collapse-tags
            style="width: 100%"
            placeholder="Select Vendor or Vendors"
          >
            <el-option
              v-for="option in vendors"
              :key="option.vendorId"
              :label="option.name"
              :value="option.vendorId"
              @click="select(option.vendorId)"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Deadline" label-position="top">
          <el-input suffix-icon="el-icon-date" v-model="data.deadline" type="date"></el-input>
          <div
            style="color: red; margin-top: 0px; font-size: 14px;"
            v-if="incorrectDate === true"
          >Incorrect date try another</div>
        </el-form-item>
        <el-form-item label="Delivery Location Of RFP Items" label-position="top">
          <el-input type="text" suffix-icon="el-icon-location" v-model="data.location"></el-input>
        </el-form-item>
        <el-form-item label="Quantity Of RFP Items" label-position="top">
          <el-input type="Number" v-model="data.quantity"></el-input>
        </el-form-item>
        <el-form-item>
          <div class="pdf-upload" @click="clickFile()">
            <span doc-name>Click to upload Document</span>
            <input type="file" class="file" @change="pdfCheck()" ref="pdf" />
          </div>
        </el-form-item>
        <span class="btns">
          <el-button class="btn" @click="sendRfqVendor">
            <i class="el-icon-loading" id="load"></i>
            <span id="text">Upload</span>
          </el-button>
        </span>
      </el-form>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import { post, openNotification } from "../../helpers/index.js";

export default {
  name: "rfp",
  beforeCreate() {
    this.$store.dispatch("getVendorsList");
    this.$store.dispatch("getVendorsCategory");
  },

  data() {
    return {
      cat: [
        {
          name: " Access Control/Security"
        },
        {
          name: "Advertising Services"
        },
        {
          name: "Appliances"
        },
        {
          name: "Architects"
        },
        {
          name: "Asbestos"
        },
        {
          name: "Athletics"
        },
        {
          name: "Audio Visual"
        },
        {
          name: "Banking Services"
        },
        {
          name: "Books, Subscriptions, & Publications"
        },
        {
          name: "Catering & Hospitality"
        },
        {
          name: "Chemicals (non-janitorial)"
        },
        {
          name: "Computers: Hardware"
        },
        {
          name: "Construction Delivery"
        },
        {
          name: "Construction Specialties"
        },
        {
          name: "Conveying Systems"
        },
        {
          name: "Electrical"
        },
        {
          name: "Electronics"
        },
        {
          name: "Employee Benefits"
        },
        {
          name: "Energy/Utilities"
        },
        {
          name: "Engineers"
        },
        {
          name: "Entertainment"
        },
        {
          name: "Flooring"
        },
        {
          name: "Food Services"
        },
        {
          name: "Furniture/Furnishings"
        },
        {
          name: "Gases and Fuels"
        },
        {
          name: "Insulation"
        },
        {
          name: "Insurance"
        },
        {
          name: "Investment Services"
        },
        {
          name: "Janitorial"
        },
        {
          name: "Legal Services"
        },
        {
          name: "Linen, Laundry, & Uniforms"
        },
        {
          name: "Masonry/Concrete"
        },
        {
          name: "Mechanical"
        },
        {
          name: "Medical Supplies/First Aid"
        },
        {
          name: "Memberships"
        },
        {
          name: "Metals"
        },
        {
          name: "Office Supplies/Equipment"
        },
        {
          name: "Painting/Interior Finishes"
        },
        {
          name: "Photographic Equipment"
        },
        {
          name: "Plumbing"
        },
        {
          name: "Postage"
        },
        {
          name: "Printing"
        },
        {
          name: "Professional Services/Consultants"
        },
        {
          name: "Promotional"
        },
        {
          name: "Relocation Services"
        },
        {
          name: "Roofing"
        },
        {
          name: "Safety/Fire"
        },
        {
          name: "Scientific Supplies"
        },
        {
          name: "Shipping Services/UPS/Customs"
        },
        {
          name: "Site/Landscaping"
        },
        {
          name: "Software"
        },
        {
          name: "Structural"
        },
        {
          name: "Subcontract Awards"
        },
        {
          name: "Telecommunications"
        },
        {
          name: "Thermal & Moisture Protection"
        },
        {
          name: "Training"
        },
        {
          name: "Transportation (charter services)"
        },
        {
          name: "Travel"
        },
        {
          name: "Utilities Specialties"
        },
        {
          name: "Vehicles/Equipment"
        },
        {
          name: "Waste Disposal and Recycling"
        },
        {
          name: "Windows/Doors/Storefronts"
        }
      ],
      type: [
        {
          value: "service",
          label: "Service"
        },
        {
          value: "business",
          label: "Business"
        },
        {
          value: "single",
          label: "Specific Vendors"
        }
      ],
      category: null,
      incorrectDate: false,
      data: {
        title: null,
        description: null,
        deadline: null,
        location: null,
        quantity: null,
        category: [],
        document: null,
        type: "service"
      }
    };
  },
  computed: {
    ...mapState(["categories", "vendors"])
  },
  methods: {
    select(id) {
      this.data.category.push(id);
    },
    clickFile() {
      document.querySelector(".file").click();
    },
    pdfCheck() {
      const fileInput = document.querySelector(".file");
      const file = fileInput.files[0];
      if (file.type !== "application/pdf") {
        openNotification("Only PDF files are acceptable", "error", this);
      } else {
        document.querySelector("[doc-name]").textContent = file.name;
        this.data.document = file;
      }
    },
    sendRfqVendor() {
      let vueApp = this;
      let presentDate = new Date();
      let value;
      if (this.data.deadline !== null) {
        value = this.data.deadline.split("-");
        value = new Date(
          parseInt(value[0]),
          parseInt(value[1]) - 1,
          parseInt(value[2])
        );
      }

      if (this.data.title === null || this.data.description === null) {
        openNotification(
          "Name and Description of RFP items are compulsory",
          "error",
          vueApp
        );
      } else {
        if (presentDate <= value) {
          document.getElementById("text").style.display = "none";
          document.getElementById("load").style.display = "block";
          const apiUrl = this.$store.state.apiUrl + "/api/v1/rfqs";
          const formData = new FormData();
          formData.append("title", this.data.title);
          formData.append("description", this.data.description);
          formData.append("deadline", this.data.deadline);
          formData.append("location", this.data.location);
          formData.append("quantity", this.data.quantity);
          formData.append("category", this.data.category);
          formData.append("type", this.data.type);
          let vueApp = this;
          if (this.data.document !== null) {
            formData.append("document", this.data.document);
          }
          post(apiUrl, formData, vueApp, () => {
            this.$store.dispatch("rfqList");
            document.getElementById("text").style.display = "block";
            document.getElementById("load").style.display = "none";
            document.getElementById("form").reset();
          });
        } else if (presentDate > value) {
          openNotification("Incorrect format of Date", "error", vueApp);
          this.incorrectDate = true;
          setTimeout(() => {
            this.incorrectDate = false;
          }, 4000);
        }
      }
    }
  }
};
</script>
<style scoped>
