  height: 100px;
  width: 200px;
  color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px dashed #eee;
  border-radius: 10px;
}
.rfp {
  width: 100%;
  padding-bottom: 5%;
}
.file {
  border: 1px dashed rgb(19, 19, 56) !important;
  display: none;
}
.form {
  margin: auto;
  width: 550px;
  margin-top: 3%;
}
.heading {
  width: inherit;
  margin-top: -3%;
  margin-right: 3%;
  margin-bottom: 30px;
  border-bottom: 1px dashed rgb(235, 224, 224);
  font-size: 30px;
}
.el-select .el-input.is-focus .el-input__inner {
  width: 100%;
}
.uploadbutton {
  background-color: rgb(35, 35, 75);
  width: 100%;
  color: #ffffff;
}
.bluebtn {
  background-color: rgb(35, 35, 75);
  width: 30%;
  color: #ffffff;
}
.pinkbtn {
  background-color: rgb(252, 104, 153);
  color: #ffffff;
  width: 30%;
}
.btn {
  background-color: rgb(35, 35, 75);
  width: 30%;
  color: #ffffff;
  /* width: 30%;
  color: rgb(35, 35, 75);
  border: 1px solid rgb(35, 35, 75); */
}
.i {
  font-size: 20px;
}
#load {
  display: none;
}

#select {
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: 0;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}
