import axios from "axios";
const regApiUrl = "/api/v1/accounts";
const loginApiUrl = "/api/v1/login";
export default {
  validEmail: email => {
    var re = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;
    return re.test(email);
  },
  reg: async (userData, vueApp) => {
    await axios
      .post(vueApp.$store.state.apiUrl + regApiUrl, userData)
      .then(() => {
        vueApp.$router.push("/email-auth");
        this.$store.state.loading = false;
      })
      .catch(err => {
        const Toast = vueApp.$swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 5000,
          type: "error",
          title: err.message
        });
        Toast.fire({
          type: "error",
          title: err.response.data.message
        });
        throw err;
      });
  },
  login: async (userData, vueApp) => {
    await axios
      .post(vueApp.$store.state.apiUrl + loginApiUrl, userData)
      .then(response => {
        if (response.data.error === true) {
          const Toast = vueApp.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "error",
            title: response.data.msg
          });
          Toast.fire({
            type: "error",
            title: response.data.msg
          });
        } else if (response.data.error === false) {
          const user = response.data;
          vueApp.$store.dispatch("setProfile", { user, vueApp });
          window.localStorage.setItem("user", JSON.stringify(response.data));
          window.localStorage.setItem(
            "JWT",
            JSON.stringify(response.data.token)
          );
          vueApp.$store.dispatch("jwt");
          if (response.data.userType === "vendor") {
            vueApp.$router.push("/vendor-dashboard");
          } else if (response.data.userType === "business") {
            vueApp.$store.dispatch("getNotifications");
            vueApp.$router.push("/admin-dashboard");
          } else if (response.data.userType === "regular") {
            vueApp.$router.push("/admin-dashboard");
          }
          const Toast = vueApp.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: "Signed in successfully"
          });
          Toast.fire({
            type: "success",
            title: "Signed in successfully"
          });
        }
      })
      .catch(err => {
        const Toast = vueApp.$swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 5000,
          type: "error",
          title: err.message
        });
        Toast.fire({
          type: "error",
          title: err.response.data.message
        });
        throw err;
      });
  }
};
