<template>
  <div class="flex">
    <div class="messagescontainer">
      <div>
        <div class="messagetitle">
          <span style="display: flex; align-items: center">
            <go-back />Messages
          </span>
          <i class="el-icon-message messageicon"></i>
        </div>
        <el-menu v-for="head in messageHead" :key="head.id">
          <el-menu-item id="item" style="border-right: transparent" @click="passhead(head)">
            <el-avatar size="medium"></el-avatar>
            <div class="messagedetails">
             
              <span class="messagenames">
               <b>{{ $format(head.senderId === userInfo.id? head.receiverName : head.senderName)}}</b>
              </span>
              <span
                class="message"
                v-if="head.last_message !== null"
              >{{$formatText(head.last_message.message)}}</span>
              <span
                class="message"
                v-else
              >Click to start conversation</span>
            </div>
          </el-menu-item>
        </el-menu>
      </div>
    </div>
    <div class="messagecontentcontainer">
      <div class="emptytextdiv" v-if="chat === null">
        <div class="emptytextheader">You dont Have a message selected</div>
        <div class="emptytext">Select a message or navigate to chat vendors</div>
        <router-link to="/vendors"></router-link>
        <el-button>Chat Vendors</el-button>
      </div>
      <message v-else />
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";

import message from "./message";
export default {
  data() {
    return {
      chat: null,
      state: this.$store.state
    };
  },
  computed: {
    ...mapState(["messageHead", "userInfo"])
  },
  beforeCreate() {
    this.$store.dispatch("getMessagesHeads");
  },
  components: {
    message
  },
  methods: {
    passhead(head) {
      this.$store.state.recieverName = head.senderId === this.$store.state.userInfo.id? head.receiverName : head.senderName;
      this.chat = head.senderId === this.$store.state.userInfo.id? head.receiverId : head.senderId;
      this.$store.state.messageConfig = {
        receiver: head.senderId === this.$store.state.userInfo.id? head.receiverId : head.senderId,
        head_id: head.id
      };
    }
  }
};
</script>
<style  scoped>
.flex {
  display: flex;
  height: calc(100% - 45px);
}
.emptytextdiv {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: auto;
}
.emptytext {
  font-size: 16px;
  margin-bottom: 5px;
}
.emptytextheader {
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 7px;
}
.messagescontainer {
  height: 100%;
  color: rgb(19, 19, 56);
  border-right: 1px solid #999;
  flex: 0.3;
}
.messagetitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
  background-color: #ffffff;
  font-size: 18px;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  border-bottom: 1px solid #eee;
}
.messageicon {
  margin-right: 20px;
}
.el-menu-item {
  border-right: transparent !important;
  border-bottom: 1px solid #eee;
  display: flex;
  align-items: center;
  color: black !important;
  padding-top: 25px !important;
  padding-bottom: 25px !important;
  text-overflow: ellipsis;
  width: 400px;
  overflow: hidden;
  white-space: nowrap;
}
#item {
  border-right: transparent !important;
  padding-top: 25px !important;
  padding-bottom: 25px !important;
}
.messagedetails {
  display: flex;
  flex-direction: column;
  width: 90%;
  justify-content: space-around;
  padding-left: 10px;
  text-overflow: ellipsis !important;
  white-space: nowrap;
  overflow: hidden;
}
.messagenames {
  height: 15px;
  font-size: 15px;
  padding-bottom: 5px;
  color: black !important;
}
.el-menu-item,
.el-submenu__title {
  height: 70px;
}
.messagecontentcontainer {
  flex: 0.7;
  height: 100%;
  display: flex;
}
</style>