<template>
  <div class="body">
    <div class="main">
      <span class="header">Search Business</span>
      <div class="container">
        <span class="containerheader">
          <el-input
            v-model="search"
            @input="checkName()"
            placeholder="Search Business name or email"
          ></el-input>
          <div class="loadercont" v-if="this.$store.state.loader == true">
            <i class="el-icon-loading"></i>
          </div>
        </span>
        <div
          class="emptycontent"
          v-if="
            Array.isArray(searchedBusiness) && searchedBusiness.length == false
          "
        >
          No business has been searched yet. <br />
          Try searching by name or email
        </div>
        <div class="content" v-for="admin in searchedBusiness" :key="admin.id">
          <div class="admin">
            <div class="credentials">
              <el-avatar class="avatar" shape="square"></el-avatar>
              <div class="name">{{ admin.name }}</div>
            </div>
            <el-button id="btn" @click="apply(admin.email)"
              ><span id="text">Apply </span>
              <i class="el-icon-loading" id="load"></i
            ></el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { mapState } from "vuex";
export default {
  data: () => {
    return {
      search: null,
      imgsrc:
        "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"
    };
  },
  computed: {
    ...mapState(["searchedBusiness"])
  },
  methods: {
    checkName() {
      this.$store.dispatch("loader", true);
      const headers = this.$store.state.jwt;
      const config = {
        headers
      };
      const api =
        this.$store.state.apiUrl +
        `/api/v1/accounts/business-search?q=${this.search}`;
      axios
        .get(api, config)
        .then(result => {
          this.$store.dispatch("setSearchedBusiness", result.data.businesses);
          this.$store.dispatch("loader", false);
        })
        
    },
    apply(email) {
      var load = document.getElementById("load");
      var text = document.getElementById("text");
      load.style.display = "block";
      text.style.display = "none";
      const data = {
        email: email
      };
      const config = {
        headers: this.$store.state.jwt
      };
      const api =
        this.$store.state.apiUrl + "/api/v1/accounts/apply-to-business";
      axios
        .post(api, data, config)
        .then(response => {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: response.data.message
          });
          Toast.fire({
            type: "success",
            title: response.data.message
          });
          load.style.display = "none";
          text.style.display = "block";
        })
        .catch(err => {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "error"
          });
          Toast.fire({
            type: "error",
            title: err.response.data.message
          });
          load.style.display = "none";
          text.style.display = "block";
        });
    }
  }
};
</script>
<style scoped>
.body {
  margin-top: 0% !important;
  padding-top: 0% !important;
  padding-left: 8%;
  overflow: auto;
  position: fixed;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  background: no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
.main {
  width: 60%;
  margin: auto;
  margin-top: 2%;
}
.header {
  text-align: start;
  display: flex;
  margin-bottom: 4%;
  font: 24px -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
    sans-serif, Apple Color Emoji, Segoe UI Emoji;
}
.container {
  border: 1px solid #eee;
}
.containerheader {
  text-align: start;
  display: flex;
  font: 24px -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
    sans-serif, Apple Color Emoji, Segoe UI Emoji;
  background-color: #fafbfc;
  padding: 1.5%;
}
.emptycontent {
  padding: 4%;
  border-top: 1px solid #eee;
  font: 22px -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
    sans-serif, Apple Color Emoji, Segoe UI Emoji;
}
.admin {
  display: flex;
  justify-content: space-between;
  padding: 2%;
  border-top: 1px solid #eee;
}
.credentials {
  display: flex;
  width: 80%;
}
.avatar {
  width: 50px;
  height: 50px;
}
.name {
  font: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
    sans-serif, Apple Color Emoji, Segoe UI Emoji;
  margin-left: 4%;
  margin-top: 2%;
}
#btn {
  color: #fff;
  background-color: #28a745;
  background-image: linear-gradient(-180deg, #34d058, #28a745 90%);
  height: 40px;
}
.loadercont {
  margin-left: 2%;
}
#load {
  display: none;
}
</style>
