<template>
  <div class="message">
    <div class="messageheader">
      <span>
        <b>{{$format(this.$store.state.recieverName)}}</b>
      </span>
      <i class="el-icon-info"></i>
    </div>
    <div class="loading" v-if="messages.length === 0">Loading...</div>
    <div v-else>
      <div class="messagecontent">
        <div class="chats" v-for="chat in messages" :key="chat.id">
          <div class="messagename" v-if="chat.sender === id">
            <div class="sender">{{chat.message}}</div>
          </div>
          <div class="messagename" v-if="chat.receiver === id">
            <div class="reciever">{{chat.message}}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="messageinput">
      <div class="inputdiv">
        <el-input v-model="message" @keydown.enter="sendMessage()"></el-input>
      </div>
      <el-button circle @click="sendMessage()">
        <i class="el-icon-s-promotion"></i>
      </el-button>
    </div>
  </div>
</template>
<script>
import pubnub from "../../helpers/references.js";
import axios from "axios";
import { mapState } from "vuex";
import cuid from "cuid";
export default {
  data() {
    return {
      message: null,
      id: this.$store.state.userInfo.id
    };
  },
  computed: {
    ...mapState(["messages"])
  },
  mounted() {},
  beforeCreate() {
    this.$store.dispatch("getMessages");
    pubnub.addListener({
      message: message => {
        this.$store.commit("setMessages", [message.message]);
        this.updateScroll();
      }
    });
    pubnub.subscribe({
      channels: [this.$store.state.userInfo.id]
    });
  },
  methods: {
    updateScroll() {
      const el = this.$el.querySelector(".messagecontent");
      el.scrollTop = el.scrollHeight;
    },
    sendMessage() {
      let config = {
        headers: this.$store.state.jwt
      };
      let data = {
        id: cuid(),
        message: this.message,
        sender: this.$store.state.userInfo.id,
        receiver: this.$store.state.messageConfig.receiver,
        head_id: this.$store.state.messageConfig.head_id
      };
      axios.post(this.$store.state.apiUrl + "/api/v1/messages", data, config);
      this.$store.commit("setMessages", [data]);
      this.message = null;
    }
  }
};
</script>
<style scoped>
.reciever {
  padding: 15px;
  background-color: #eee;
  margin-top: 4px;
  float: left;
  border-right: 25px;
  border-left: 20px;
  border-top-left-radius: 20px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  font-size: 14px;
  margin-right: 160px;
}
.loading {
  display: flex;
  align-items: center;
  justify-content: center;
}
.sender {
  float: right;
  padding: 10px;
  border-right: 25px;
  border-left: 25px;
  background-color: #eee;
  margin-left: 20px;
  margin-top: 4px;
  border-radius: 13px;
  margin-left: 160px;
  border-top-right-radius: 20px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 0px;
  font-size: 14px;
}
.el-input__inner {
  border-radius: 25px;
}
.message {
  height: 100%;
  width: 100%;
  flex-direction: column;
  overflow: hidden !important;
  position: relative;
}
.messagecontent {
  height: 470px;
  overflow: auto !important;
  padding: 10px;
}
.chats {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.messageheader {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  z-index: 1;
  border-left: 1px solid #eee;
  position: sticky;
  background-color: #ffffff;
  font-size: 18px;
  right: 0;
  border-bottom: 1px solid #eee;
}
.messageinput {
  display: flex;
  padding: 15px;
  z-index: 1;
  margin-left: 10px;
  bottom: 0;
  background-color: #ffffff;
  font-size: 18px;
  position: absolute;
  right: 0;
  left: 0;
  border-bottom: 1px solid #eee;
}
.inputdiv {
  width: 90%;
  margin-right: 25px;
}
</style>