<template>
  <div class="body">
    <div class="d-flex justify">
      <history-go-back />
      <div>
        <div class="modalbuttondiv">
          <el-button
            class="addbtn"
            v-if="type === 'product'"
            @click="change('service')"
          >Add service Instead</el-button>
          <el-button
            class="addbtn"
            v-if="type === 'service'"
            @click="change('product')"
          >Add Product Instead</el-button>
        </div>
      </div>
    </div>
    <div class="background">
      <div class="formcont" v-if="type === 'service'">
        <div class="breadcrumb">
          <bread-crumb name="Add Service" />
        </div>
        <el-form class="form" id="myForm">
          <el-form-item label="Name of Service" label-position="top">
            <el-input type="text" v-model="serviceData.name" required></el-input>
          </el-form-item>
          <el-form-item label="Description Of Service" label-position="top">
            <el-input
              type="textarea"
              autosize
              placeholder="Please input"
              v-model="serviceData.description"
              required
            ></el-input>
          </el-form-item>
          <el-form-item>
            <div class="pdf-upload" @click="clickFile()">
              <span doc-name>Click to upload Service Image</span>
              <input type="file" class="file" @change="pdfCheck()" ref="pdf" />
            </div>
          </el-form-item>
          <el-form-item label="Currency(Optional)">
            <select placeholder="Pick Currency" id="select" v-model="productData.currency">
              <option
                v-for="option in currencies"
                :key="option.name"
                :label="option.name"
                :value="option.name"
                id="option"
              ></option>
            </select>
          </el-form-item>
          <el-form-item label="Price of Service(Optional)" label-position="top">
            <el-input type="Number" v-model="serviceData.price" required></el-input>
          </el-form-item>
          <span>
            <el-button class="pinkbtn" @click="addProduct('service')">
              <span id="servicetext">Add Product</span>
              <i class="el-icon-loading" id="serviceload"></i>
            </el-button>
          </span>
        </el-form>
      </div>
      <div class="formcont" v-if="type === 'product'">
        <div class="breadcrumb">
          <bread-crumb name="Add Product" />
        </div>
        <el-form class="form" id="myForm">
          <el-form-item label="Name of Item" label-position="top">
            <el-input type="text" v-model="productData.name" required></el-input>
          </el-form-item>
          <el-form-item label="Description Of Item" label-position="top">
            <el-input
              type="textarea"
              autosize
              placeholder="Please input"
              v-model="productData.description"
            ></el-input>
          </el-form-item>
          <el-form-item label="Original Equipment Manufacturer" label-position="top">
            <el-input v-model="productData.oem" required></el-input>
          </el-form-item>
          <el-form-item label="Stock Keeping Unit/ Part Number" label-position="top">
            <el-input v-model="productData.sku"></el-input>
          </el-form-item>
          <el-form-item label="Minimum Order Quantity" label-position="top">
            <el-input v-model="productData.moq" required type="number"></el-input>
          </el-form-item>
          <el-form-item>
            <div class="pdf-upload" @click="clickFile()">
              <span doc-name>Click to upload Product Image</span>
              <input type="file" class="file" @change="pdfCheck()" ref="pdf" />
            </div>
          </el-form-item>
          <el-form-item label="Currency">
            <select v-model="productData.currency" placeholder="Pick Currency" id="select">
              <option
                v-for="option in currencies"
                :key="option.name"
                :label="option.name"
                :value="option.name"
                id="option"
              ></option>
            </select>
          </el-form-item>
          <el-form-item label="Price of Item" label-position="top">
            <el-input type="Number" v-model="productData.price" required></el-input>
          </el-form-item>
          <span>
            <el-button class="pinkbtn" @click="addProduct('product')">
              <span id="producttext">Add Product</span>
              <i class="el-icon-loading" id="productload"></i>
            </el-button>
          </span>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
import { post } from "../helpers/index.js";
export default {
  data() {
    return {
      type: "product",
      serviceData: {
        name: null,
        description: null,
        price: null,
        type: "service",
        picture: null,
        currency: null
      },
      productData: {
        name: null,
        description: null,
        oem: null,
        price: null,
        type: "product",
        moq: null,
        sku: null,
        picture: null,
        currency: null
      },
      currencies: [
        {  name: "US Dollar" },
        {  name: "Euro" },
        { name: "Japanese Yen" },
        {  name: "Australian Dollar" },
        {  name: "Canadian Dollar" },
        {  name: "Swiss Franc" },
        {  name: "Chinese renminbi" },
        {  name: "Swedish krona" },
        {  name: "New Zealand dollar " },
        {  name: "Nigerian Naira" },
      ]
    };
  },
  methods: {
    change(choice) {
      this.type = choice;
    },
    clickFile() {
      document.querySelector(".file").click();
    },
    pdfCheck() {
      const fileInput = document.querySelector(".file");
      const file = fileInput.files[0];
      if (this.type === "product") {
        this.productData.picture = file;
        document.querySelector("[doc-name]").textContent = file.name;
      } else if (this.type === "service") {
        this.serviceData.picture = file;
        document.querySelector("[doc-name]").textContent = file.name;
      }
    },
    async addProduct(value) {
      var load;
      var text;
      let formData = new FormData();
      if (value === "service") {
        load = document.getElementById("serviceload");
        text = document.getElementById("servicetext");
        load.style.display = "block";
        text.style.display = "none";
        formData.append("name", this.serviceData.name);
        formData.append("description", this.serviceData.description);
        formData.append("price", this.serviceData.price);
        formData.append("type", "service");
        formData.append("picture", this.serviceData.picture);
      } else if (value === "product") {
        load = document.getElementById("productload");
        text = document.getElementById("producttext");
        load.style.display = "block";
        text.style.display = "none";
        formData.append("name", this.productData.name);
        formData.append("description", this.productData.description);
        formData.append("oem", this.productData.oem);
        formData.append("price", this.productData.price);
        formData.append("type", this.productData.type);
        formData.append("moq", this.productData.moq);
        formData.append("sku", this.productData.sku);
        formData.append("picture", this.productData.picture);
      }
      
      var api = this.$store.state.apiUrl + "/api/v1/accounts/vendors/inventory";
      try {
        await post(api, formData, this, () => {
          load.style.display = "none";
          text.style.display = "block";
          this.$store.dispatch("inventory");
          document.getElementById("myForm").reset();
        });
      } catch (_) {
        load.style.display = "none";
        text.style.display = "block";
      }
    }
  }
};
</script>
<style scoped>
.body {
  padding-top: 15px;
}
.pdf-upload {
  height: 100px;
  width: 200px;
  color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px dashed #eee;
  border-radius: 10px;
}
.pdf-upload {
  height: 100px;
  width: 200px;
  color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px dashed #eee;
  border-radius: 10px;
}
.file {
  border: 1px dashed rgb(19, 19, 56) !important;
  display: none;
}
.go-back {
  margin-left: 15px;
}
.justify {
  justify-content: space-between;
  padding: 5px;
}
.el-select {
  width: 100%;
}
.formcont {
  width: 500px;
  display: flex;
  flex-direction: column;
  margin: auto;
}
.breadcrumb {
  margin: auto;
  text-align: center;
}
.form {
  width: 100%;
}
.heading {
  width: inherit;
  margin-top: -3%;
  margin-right: 3%;
  margin-bottom: 30px;
  border-bottom: 1px dashed rgb(235, 224, 224);
}
.uploadbutton {
  background-color: #0066f5;
  width: 100%;
  color: #ffffff;
}
.bluebtn {
  background-color: #0066f5;
  width: 30%;
  color: #ffffff;
}
.pinkbtn {
  color: #fff;
  background-color: #28a745;
  background-image: linear-gradient(-180deg, #34d058, #28a745 90%);
}
.btn {
  width: 30%;
  color: #0066f5;
  border: 1px solid #0066f5;
}
#productload {
  display: none;
  font-size: 20px;
  color: #ffffff;
}
#serviceload {
  display: none;
  font-size: 20px;
  color: #ffffff;
}
#select {
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: 0;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}
</style>
