    async submitForm() {
      const vueApp = this;
      this.loading = true;
      auth.validEmail(this.data.email);
      if (auth.validEmail(this.data.email)) {
        if (this.data.password === this.cpassword) {
          try {
            await auth.reg(this.data, vueApp);
          } catch (_) {
            this.loading = false;
          }
        } else {
