<template>
  <div class="body">
    <navbar />
    <div style="display: flex; margin-top: 0px">
      <go-back />
    </div>
    <el-card class="card">
      <div class="title">Your Profile</div>
      <div
        @click="clickFile()"
        style="display: flex; justify-content: center; margin-top: 5px; margin-bottom: 5px; align-items: center;width: 90px; margin: auto;"
      >
        <el-avatar
          shape="circle"
          :src="this.$store.state.userInfo.avatar"
          style="width: 70px; height: 70px;"
        ></el-avatar>
        <i
          v-if="this.$store.state.userInfo.avatar !== null || this.$store.state.userInfo.avatar"
          class="el-icon-edit"
          style="color: white;border-radius: 50%; width: 20px;height: 20px;margin-left: -5px;margin-top: 15px;background-color: black;"
        ></i>
      </div>

      <el-form class="form">
        <el-container class="valuecontainer">
          <el-container class="valuetitle">Name:</el-container>
          <el-container class="value">{{$format(this.$store.state.userInfo.name)}}</el-container>
        </el-container>
        <el-container class="valuecontainer">
          <el-container class="valuetitle">Email:</el-container>
          <el-container class="value">{{$format(this.$store.state.userInfo.email)}}</el-container>
        </el-container>
        <el-container class="valuecontainer">
          <el-container class="valuetitle">Phone Number:</el-container>
          <el-container class="value">{{this.$store.state.userInfo.phone}}</el-container>
        </el-container>
        <el-container class="valuecontainer">
          <el-container class="valuetitle">Account Type:</el-container>
          <el-container class="value">{{$format(this.$store.state.userInfo.userType)}}</el-container>
        </el-container>
        <el-container class="valuecontainer">
          <el-container class="valuetitle">Location:</el-container>
          <el-container class="value">{{$format(this.$store.state.userInfo.location)}}</el-container>
        </el-container>
        <el-form-item>
          <div class="pdf-upload" @click="clickFile()" style="display: none;">
            <span doc-name>Click to upload Profile Picture</span>
            <input type="file" class="file" @change="pdfCheck()" ref="pdf" />
          </div>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import navbar from "../components/vendornavbar.vue";
import { post } from "../helpers/index.js";
export default {
  data() {
    return {
      dialogImageUrl: "",
      dialogVisible: false,
      disabled: false
    };
  },
  components: {
    navbar
  },
  beforeCreate() {
    this.$store.dispatch("getAccountDetails");
  },
  methods: {
    clickFile() {
      // e.preventDefault();
      document.querySelector(".file").click();
    },
    pdfCheck() {
      const fileInput = document.querySelector(".file");
      const file = fileInput.files[0];
      if (file.type !== "image/jpeg" && file.type !== "image/png") {
        alert("Only Images with extension jpeg and png are acceptable");
      } else {
        document.querySelector("[doc-name]").textContent = file.name;
        let formData = new FormData();
        formData.append("image", file);
        post(
          this.$store.state.apiUrl + "/api/v1/accounts/upload-avatar",
          formData,
          this,
          () => {
            this.$store.dispatch("getAccountDetails");
          }
        );
      }
    }
  }
};
</script>
<style scoped>
.title {
  text-align: center;
  font-size: 14px;
}
.valuetitle {
  text-align: start;
  width: 50px;
  padding: 15px;
  flex: 0.4;
}
.valuecontainer {
  display: flex;
}
.value {
  text-align: start !important;
  padding: 15px;
  flex: 0.6;
}
.form {
  margin-top: 50px;
  width: 400px;
  margin: auto;
}
.card {
  width: 450px;
  margin: auto;
  padding: 0%;
}
.pdf-upload {
  height: 100px;
  width: 200px;
  color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px dashed #eee;
  border-radius: 10px;
}
.file {
  border: 1px dashed rgb(19, 19, 56) !important;
  display: none;
}
.body {
  padding-right: 0% !important;
  margin-top: 0% !important;
  overflow: auto !important;
  padding-top: 0px;
  padding-bottom: 40px;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background: no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
.heading {
  width: inherit;
  margin-top: -3%;
  margin-right: 3%;
  margin-bottom: 30px;
  border-bottom: 1px dashed rgb(235, 224, 224);
  font-size: 30px;
}
.uploadbutton {
  background-color: #0066f5;
  width: 100%;
  color: #ffffff;
}
.bluebtn {
  background-color: #0066f5;
  width: 30%;
  color: #ffffff;
}
.pinkbtn {
  background-color: rgb(252, 104, 153);
  color: #ffffff;
  width: 30%;
}
.btn {
  width: 30%;
  color: #0066f5;
  border: 1px solid #0066f5;
}
.i {
  font-size: 20px;
}
</style>
