import Vue from "vue";
import Vuex from "vuex";
import VuexPersist from "vuex-persist";
import axios from "axios";

Vue.use(Vuex);
const vuexLocalStorage = new VuexPersist({
  key: "vuex", // The key to store the state on in the storage provider.
  storage: window.localStorage, // or window.sessionStorage or localForage
  // Function that passes the state and returns the state with only the objects you want to store.
  reducer: state => ({
    userInfo: state.userInfo,
    accountNots: state.accountNots,
    jwt: state.jwt,
    // businesses: state.businesses,
    dashboardInfo: state.dashboardInfo
  })
  // Function that passes a mutation and lets you decide if it should update the state in localStorage.
  // filter: mutation => (true)
});
export default new Vuex.Store({
  plugins: [vuexLocalStorage.plugin],
  state: {
    userInfo: null,
    searchedBusiness: [],
    admins: [],
    vendors: [],
    catVendors: [],
    accountNots: [],
    documentNots: [],
    docs: [],
    pendingBusinessDetailsAvatar: null,
    apiUrl: "http://vendor-alliance-voda-api.apps.us-west-1.starter.openshift-online.com",
    jwt: null,
    loader: false,
    inventory: [],
    rfqList: null,
    businesses: [],
    categories: [],
    category: null,
    dashboardInfo: null,
    loading: false,
    chosenCat: null,
    card: false,
    requirements: null,
    unCatVendors: null,
    explorePersonal: [],
    explorePublic: [],
    exploreBusiness: [],
    searchItem: null,
    searchParams: null,
    searchedVendor: null,
    addVendorEmail: null,
    addVendorId: null,
    addVendorName: null,
    statutoryRequirements: [],
    documentRequirements: [],
    pendingBusinesses: [],
    pendingBusinessDetails: null,
    existingFulfillments: null,
    changeRoute: false,
    empty: true,
    rfpId: null,
    singleVendorId: null,
    messengersId: null,
    messages: [],
    recieverName: null,
    messageHead: null,
    messageConfig: {
      receiver: null,
      head_id: null
    },
    viewRfp: null,
    quotes: [],
    acceptedQuotes: [],
    documents: [],
    viewRfpSender: null,
    fulfillmentsresponse: null
  },
  mutations: {
    setProfile(state, user) {
      state.userInfo = user;
    },
    setSearchedBusiness(state, result) {
      state.searchedBusiness = result;
    },
    setAdmins(state, people) {
      state.admins = people;
    },
    setVendors(state, people) {
      state.vendors = people;
    },
    setCatVendors(state, people) {
      state.catVendors = people;
    },
    setUnCatVendors(state, people) {
      state.unCatVendors = people;
    },
    setAccountNots(state, payload) {
      state.accountNots = payload;
    },
    setInventory(state, data) {
      state.inventory = data;
    },
    updateAccountNots(state, payload) {
      state.accountNots.unshift(payload);
    },
    setDocs(state, payload) {
      state.docs.push(payload);
    },
    setJwt(state, payload) {
      state.jwt = payload;
    },
    loader(state, payload) {
      state.loader = payload;
    },
    rfqList(state, payload) {
      state.rfqList = payload;
    },
    setBusinesses(state, payload) {
      state.businesses = payload;
    },
    setPendingBusinesses(state, payload) {
      state.pendingBusinesses = payload;
    },
    setVendorCategory(state, category) {
      state.categories = category;
    },
    dashboardInfo(state, payload) {
      state.dashboardInfo = payload;
    },
    setStatutoryRequirements(state, payload) {
      state.statutoryRequirements = payload;
    },
    setDocumentRequirements(state, payload) {
      state.documentRequirements = payload;
    },
    setMessagesHeads(state, payload) {
      state.messageHead = payload
    },
    setMessages(state, payload) {
      state.messages.push(...payload)
    },
    setDocument(state, payload) {
      state.documents = payload
    }
  },
  actions: {
    getAccountDetails({ state, commit }) {
      let config = {
        headers: state.jwt
      };
      axios.get(state.apiUrl + '/api/v1/accounts', config).then(result => {
        commit("setProfile", result.data);
      })

    },
    setProfile({ commit }, { user }) {
      commit("setProfile", user);
    },
    setSearchedBusiness({ commit }, result) {
      commit("setSearchedBusiness", result);
    },
    getVendorsList({ commit, state }, cat = null) {
      const jwt = state.jwt;
      const config = {
        headers: jwt
      };
      if (cat !== null) {
        state.category = cat;
        axios
          .get(
            state.apiUrl + `/api/v1/accounts/vendors?category=${cat}`,
            config
          )
          .then(result => {
            const people = result.data;
            commit("setCatVendors", people.vendors)
          });
      } else {
        axios
          .get(state.apiUrl + "/api/v1/accounts/vendors", config)
          .then(result => {
            const people = result.data;
            commit("setVendors", people.vendors);
          })
          .catch();
      }
    },
    getUnCatVendors({ commit, state }, cat) {
      const jwt = state.jwt;
      const config = {
        headers: jwt
      };
      axios
        .get(state.apiUrl + `/api/v1/accounts/vendors?except=${cat}`, config)
        .then(result => {
          const people = result.data;
          commit("setUnCatVendors", people.vendors);
        })
        .catch();
    },
    getAdminList({ commit, state }) {
      const jwt = state.jwt;
      const config = {
        headers: jwt
      };
      axios
        .get(state.apiUrl + "/api/v1/accounts/admins", config)
        .then(result => {
          const people = result.data.admins;
          commit("setAdmins", people);
        });
    },
    dashboardInfo({ state, commit }) {
      if (state.userInfo.userType === "business") {
        const url = state.apiUrl + "/api/v1/ui/business-dashboard-data";
        const jwt = state.jwt;
        const config = {
          headers: jwt
        };
        axios.get(url, config).then(result => {
          commit("dashboardInfo", result.data.extras);
        });
      } else if (state.userInfo.userType === "vendor") {
        const url = state.apiUrl + "/api/v1/ui/vendor-dashboard-data";
        const jwt = state.jwt;
        const config = {
          headers: jwt
        };
        axios.get(url, config).then(result => {
          commit("dashboardInfo", result.data.extras);
        });
      }
    },
    getBusinessList({ commit, state }) {
      const jwt = state.jwt;
      const config = {
        headers: jwt
      };
      axios
        .get(
          state.apiUrl + "/api/v1/accounts/vendors/businesses?pending=0",
          config
        )
        .then(result => {
          const people = result.data.businesses;
          commit("setBusinesses", people);
        });
    },
    getPendingBusinessList({ commit, state }) {
      const jwt = state.jwt;
      const config = {
        headers: jwt
      };
      axios
        .get(
          state.apiUrl + "/api/v1/accounts/vendors/businesses?pending=1",
          config
        )
        .then(result => {
          const people = result.data.businesses;
          commit("setPendingBusinesses", people);
        })
    },
    getRequirements({ commit, state }) {
      const headers = state.jwt;
      const apiUrl = state.apiUrl + "/api/v1/accounts/requirements";
      axios
        .get(apiUrl, { headers })
        .then(results => {
          //sets statutory requirement
          commit(
            "setStatutoryRequirements",
            results.data.requirements.statutory
          );
          //sets document requirement
          commit("setDocumentRequirements", results.data.requirements.document);
          if (state.documentRequirements.length === 0 && state.statutoryRequirements.length === 0) {
            state.empty = true;
          } else {
            state.empty = false
          }
        })
    },
    getNotifications({ commit, state }) {
      const headers = state.jwt;
      const apiUrl = state.apiUrl + "/api/v1/notifications";
      axios.get(apiUrl, { headers }).then(results => {
        commit("setAccountNots", results.data.data);
      })
    },
    inventory({ commit, state }, id = null) {
      const config = {
        headers: state.jwt
      };
      if (id === null) {
        const apiUrl = state.apiUrl + "/api/v1/accounts/vendors/inventory";
        axios.get(apiUrl, config).then(result => {

          commit("setInventory", result.data.data);
        });
      } else {
        const apiUrl =
          state.apiUrl + `/api/v1/accounts/vendors/inventory?id=${id}`;
        axios
          .get(apiUrl, config)
          .then(result => {
            commit("setInventory", result.data.data);
          })
      }
    },
    logout({ commit }) {
      window.localStorage.removeItem("user");
      window.localStorage.removeItem("JWT");
      window.localStorage.removeItem("vuex");
      commit("setProfile", null);
      commit("setAccountNots", []);
      commit("setJwt", null);
    },
    rfqList({ commit, state }) {
      const config = {
        headers: state.jwt
      };
      axios.get(state.apiUrl + "/api/v1/rfqs", config).then(result => {
        const data = result.data.data;
        commit("rfqList", data);
      });
      // .catch(err => {
      // })
    },
    jwt({ commit }) {
      const jwt = window.localStorage.getItem("JWT");
      const JWT = JSON.parse(jwt);
      const headers = {
        Authorization: `Bearer ${JWT}`
      };
      commit("setJwt", headers);
    },
    getDocs({ commit }, checkList) {
      commit("setDocs", checkList);
    },
    loader({ commit }, val) {
      commit("loader", val);
    },
    getVendorsCategory({ commit, state }) {
      const jwt = state.jwt;
      const config = {
        headers: jwt
      };
      let categories;
      axios
        .get(state.apiUrl + "/api/v1/accounts/business/category", config)
        .then(result => {
          categories = result.data.categories;
          commit("setVendorCategory", categories);
        });
    },
    explore({ state}) {
      const headers = state.jwt;
      axios
        .get(state.apiUrl + `/api/v1/rfqs/explore?type=private`, {headers})
        .then(result => {
          state.explorePersonal = result.data.data;
        })
      axios
        .get(state.apiUrl + `/api/v1/rfqs/explore`, {headers})
        .then(result => {
          state.explorePublic = result.data.data
        })
      axios
        .get(state.apiUrl + `/api/v1/rfqs/explore?type=private-group`, {headers})
        .then(result => {
          state.exploreBusiness = result.data.data
        })
    },
    getMessagesHeads({ commit, state }) {
      const config = {
        headers: state.jwt
      }
      axios.get(state.apiUrl + '/api/v1/messages/heads', config).then(result => {
        commit('setMessagesHeads', result.data.data)
      })
    },
    getMessages({ commit, state }) {
      const config = {
        headers: state.jwt
      }
      axios.get(state.apiUrl + `/api/v1/messages?head_id=${state.messageConfig.head_id}`, config).then(result => {
        commit('setMessages', result.data.messages)
      })
    },
    getQuotes({ state }) {

      axios
        .get(state.apiUrl + "/api/v1/business/quotes", {
          headers: state.jwt
        })
        .then(result => {
          state.quotes = result.data.quotes;
        });
    },
    initiateChat({ state }, { vendorId, vueApp }) {
      let config = {
        headers: state.jwt
      }
      let data = {
        id: vendorId,
        type: 'vendor'
      }
      axios.post(state.apiUrl + '/api/v1/messages/heads', data, config)
      vueApp.$router.push('/messages')
    },
    getDocuments({ commit, state }) {
      axios.get(state.apiUrl + '/api/v1/archives', { headers: state.jwt })
        .then(result => {
          commit('setDocument', result.data.data)
        })
    }
  },
  modules: {}
});
