<template>
  <div class="body">
    <div class="buisness">
      <div class="admincontainer">
        <div class="admincontainerheader">
          <b>BUSSINESSES</b>
        </div>
        <div class="adminempty" v-if=" this.$store.state.businesses.length === 0 ">
          <div class="adminemptyheader">No business yet</div>
          <router-link to="/search-business">
            <el-button class="adminemptybutton emptybtn">Search to apply to business</el-button>
          </router-link>
        </div>
        <div  v-if="this.$store.state.businesses.length !== 0"><div class="admins" v-for="business in businesses" :key="business.id">
          <div class="admin">
            <div class="creds">
              <el-avatar shape="square" class="avatar"></el-avatar>
              <h3 class="adminname">{{ $format(business.name) }}</h3>
            </div>
          </div>
        </div></div>
        
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { mapState } from "vuex";
export default {
  data() {
    return {
      centerDialogVisible: false,
      category: {
        name: null,
        category: null
      },
      name: null,
      timeout: null
    };
  },
  computed: {
    ...mapState(["businesses"])
  },
  beforeCreate() {
    this.$store.dispatch("getBusinessList");
  },
  methods: {
    delist(vendorEmail) {
      const headers = this.$store.state.jwt;
      const data = {
        email: vendorEmail
      };
      axios
        .delete(this.$store.state.apiUrl + "/api/v1/accounts/vendors", {
          data,
          headers
        })
        .then(result => {
          this.$store.dispatch("getVendorsList");
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: result.data.message
          });
          Toast.fire({
            type: "success",
            title: result.data.message
          });
        })
        .catch(err => {
          this.$store.dispatch("swalErr", err.response.data);
        });
    }
  }
};
</script>
<style scoped>
.body {
  width: 100%;
  padding-bottom: 5%;
}
.goback {
  padding-right: 9px;
}
.head {
  display: flex;
  justify-content: space-between;
  width: 800px;
  margin-left: 14%;
  padding-top: 15px;
}
.emptybtn{
  margin-top: 20px!important;
}
</style>
