<template>
  <div class="body">
    <go-back />
    <div class="adminheader">
      <el-autocomplete
        v-model="name"
        @input="search"
        placeholder="Please input admin email"
      >
        <i class="el-icon-search el-input__icon" slot="suffix"></i>
      </el-autocomplete>
      <el-button class="greenbtn" @click="card()">New Admin</el-button>
      <div id="modal" v-if="this.$store.state.card === true">
        <el-card class="modalcard">
          <div class="modalbuttondiv">
            <el-button circle class="modalclosebtn" @click="closeModal()">
              <i class="el-icon-close"></i>
            </el-button>
          </div>
          <el-avatar
            class="modalavatar"
            src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
          ></el-avatar>
          <el-form class="form" ref="form" id="form">
            <el-form-item label="Admin's Email" label-position="top">
              <el-input v-model="userData.email"></el-input>
            </el-form-item>
            <el-form-item v-loading="loading">
              <el-button
                class="modalbtn"
                @click="makeAdmin"
                v-if="loading === false"
              >
                <span id="text">Add Admin</span>
              </el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </div>
    </div>
    <div class="admincontainer">
      <div class="admincontainerheader">
        <b>Present Admins</b>
      </div>
      <div class="adminempty" v-if="this.$store.state.admins.length === 0">
        <div class="adminemptyheader">No Admins yet</div>
          <el-button class="adminemptybutton emptybtn" @click="card()">Add Admins</el-button>
      </div>
      <div class="admins" v-for="admin in admins" :key="admin.id">
        <div class="admin">
          <div class="admincreds">
            <el-avatar shape="square"></el-avatar>
            <h3 class="adminname">{{ $format(admin.name )}}</h3>
          </div>
          <el-button
            class="delbtn"
            @click="delist(admin.email, admin)"
            v-loading="loading"
          >
            Delist
          </el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import auth from "../../auth/index.js";
import { mapState } from "vuex";
export default {
  data() {
    return {
      centerDialogVisible: false,
      apiUrl: this.$store.state.apiUrl + "/api/v1/accounts/admins",
      userData: {
        email: null
      },
      users: [],
      name: null,
      timeout: null,
      loading: false
    };
  },
  computed: {
    ...mapState(["admins"])
  },
  beforeCreate() {
    this.$store.dispatch("getAdminList");
  },
  methods: {
    card() {
      this.$store.state.card = true;
    },
    closeModal() {
      this.$store.state.card = false;
    },
    makeAdmin() {
      this.loading = true;
      const config = {
        headers: this.$store.state.jwt
      };
      if (auth.validEmail(this.userData.email)) {
        axios
          .post(this.apiUrl, this.userData, config)
          .then(response => {
            if (response.data.error === true) {
              const Toast = this.$swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 5000,
                type: "error",
                title: response.data.error
              });
              Toast.fire({
                type: "error",
                title: response.data.error
              });
            } else {
              this.$store.dispatch("getAdminList");
              const Toast = this.$swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 5000,
                type: "success",
                title: response.data.message
              });
              Toast.fire({
                type: "success",
                title: response.data.message
              });
            }
          })
          .catch(err => {
            const Toast = this.$swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 5000,
              type: "error",
              title: err
            });
            Toast.fire({
              type: "error",
              title: err.response.data.message
            });
          });
      } else {
        const Toast = this.$swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 5000,
          type: "error",
          title: "Incorrect format of email"
        });
        Toast.fire({
          type: "error",
          title: "Incorrect format of email"
        });
      }
      this.$store.dispatch("getAdminList");
      this.loading = false;
      document.getElementById("form").reset();
      this.$store.state.card = false;
    },
    delist(adminEmail, obj) {
      this.loading = true;
      const headers = this.$store.state.jwt;
      const data = {
        email: adminEmail
      };
      axios
        .delete(this.$store.state.apiUrl + "/api/v1/accounts/admins", {
          data,
          headers
        })
        .then(result => {
          this.$store.state.admins.splice(
            this.$store.state.admins.indexOf(obj),
            1
          );
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: result.data.message
          });
          Toast.fire({
            type: "success",
            title: result.data.message
          });
          this.loading = false;
        })
        .catch(err => {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: err.response.data.message
          });
          Toast.fire({
            type: "success",
            title: err.response
          });
        });
      this.loading = false;
    },
    search() {
      const config = {
        headers: this.$store.state.jwt
      };
      const name = this.name;
      const apiUrl =
        this.$store.state.jwt +
        `/api/v1/accounts/user-search?q=<query>&by=${name}`;
      axios.get(apiUrl, config).then(result => {
        this.$store.dispatch("setSearchedBusiness", result.data.businesses);
      });
      // .catch(err => {
      //   alert(err)
      // })
    }
  }
};
</script>
<style scoped>
.body {
  width: 100%;
  padding-bottom: 5%;
}
.btn {
  width: 100%;
  background-color: #0066f5;
  color: #ffffff;
}

.modalavatar {
  margin: auto;
  display: flex;
  justify-self: center;
}
.modalbtn {
  width: 100%;
  background-color: #0066f5;
  color: #ffffff;
}
</style>
