<template>
  <div style="padding: 10px">
    <div id="modal" v-if="modal === true">
      <el-card class="modalcard" v-if="del === true">
        <div class="del-pop-up-btn">
          <i class="el-icon-delete"></i>
        </div>
        <div class="del-pop-up-header">Delete Document</div>
        <div class="del-pop-up-text">Are you sure you want to delete this Document</div>
        <div style="display: flex; justify-content: center; margin-top: 4px">
          <el-button class="del-action-pop-up-btn" @click="handleDelete()">Delete</el-button>
          <el-button @click="closeDelPopUp()">Dont Delete</el-button>
        </div>
      </el-card>
      <el-card class="modalcard" v-if="input === true" style="padding-top: 0px">
        <div class="modalbuttondiv">
          <el-button class="modalclosebtn" @click="cancel()">
            <i class="el-icon-close"></i>
          </el-button>
        </div>
        <el-form>
          <el-form-item label="Document Name">
            <el-input v-model="name"></el-input>
          </el-form-item>
          <el-button
            class="greenbtn"
            style="width: 100%"
            @click="archive()"
            v-loading="docAddLoading"
          >Add to Archives</el-button>
        </el-form>
      </el-card>
    </div>
    <div class="modalbuttondiv">
      <el-button @click="clickFile()" class="greenbtn">Add to Documents</el-button>
      <input type="file" class="file" @change="pdfCheck()" style="display: none" />
    </div>
    <span class="title">Select Document</span>
    <el-table :data="documents" style="width: 100%" v-if="documents.length !== 0">
      <el-table-column label="Date" width="180">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">
            {{
            new Date(scope.row.createdAt).getDate() +
            ' - ' + (new Date(scope.row.createdAt).getMonth() + 1) +
            ' - ' + new Date(scope.row.createdAt).getFullYear()}}
          </span>
        </template>
      </el-table-column>
      <el-table-column label="Name" width="180">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>Name: {{ scope.row.name }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{ scope.row.name }}</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column label="Type">
        <i class="el-icon-document"></i>
      </el-table-column>
      <el-table-column label="Operations">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="dele(scope.row)">Delete</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div
      v-if="documents.length === 0"
      style="margin: auto; display: flex; justify-content: center; align-items: center;font-size: 18px; height: calc(100vh - 250px); font-weigh: bold;"
    >No Document has been added to your Documents</div>
  </div>
</template>
<script>
import axios from "axios";
import { mapState } from "vuex";
import { post } from "../helpers/index.js";
export default {
  name: "documents",
  data() {
    return {
      del: false,
      doc: null,
      documentToUpload: null,
      modal: false,
      input: false,
      name: null
    };
  },
  computed: {
    ...mapState(["documents"])
  },
  mounted() {
    this.$store.dispatch("getDocuments");
  },
  methods: {
    dele(doc) {
      this.doc = doc;
      this.modal = true;
      this.del = true;
    },
    closeDelPopUp() {
      this.del = false;
    },
    handleDelete() {
      let data = {
        id: this.doc.id
      };
      let headers = this.$store.state.jwt;

      axios
        .delete(this.$store.state.apiUrl + "/api/v1/archives", {
          data,
          headers
        })
        .then(result => {
          this.$store.dispatch("getDocuments");
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 5000,
            type: "success",
            title: result.data.message
          });
          Toast.fire({
            type: "success",
            title: result.data.message
          });
        })
      this.del = false;
      this.modal = false;
    },
    archive() {
      this.docAddLoading = true;
      let data = new FormData();
      data.append("document", this.documentToUpload);
      data.append("name", this.name);
      let vueApp = this;
      post(this.$store.state.apiUrl + "/api/v1/archives", data, vueApp, () => {
        this.modal = false;
        this.input = false;
        this.docAddLoading = false;
        this.$store.dispatch("getDocuments");
      });
    },
    clickFile() {
      document.querySelector(".file").click();
    },
    pdfCheck() {
      const fileInput = document.querySelector(".file");
      const file = fileInput.files[0];
      if (file.type !== "application/pdf") {
        alert("Only PDF files are acceptable");
      } else {
        this.documentToUpload = file;
        this.modal = true;
        this.input = true;
      }
    }
  }
};
</script>
<style scoped>
.title {
  font-weight: bold;
  font-size: 17px;
}
.grey-area {
  background-color: #eee;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  font-size: 14px;
  margin-top: 5px;
}
.document {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}
</style>